import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewStaffData from './ViewStaffData';

class Staff extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            password: '',
            cpassword: '',
            contactno: '',
            address: '',
            allStaffArr: '',
            singleStaffArr: '',
            editStaff: false,
            openform: false,
        }
        this.getAllStaff = this.getAllStaff.bind(this)
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.editStaff = this.editStaff.bind(this)
        this.updateStaff = this.updateStaff.bind(this)
        this.addnewStaff = this.addnewStaff.bind(this)
    }


    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    componentDidMount() {
        this.getAllStaff()
    }

    getAllStaff() {
        axios.get(`${apiService.apiUrl()}/users/staffall`).then(response => {
            console.log(response.data);
            this.setState({
                allStaffArr: response.data
            })
        })
    }

    onClickSubmit() {
        const dataObject = {
            name: this.state.name,
            email: this.state.email,
            password: this.state.password,
            contactno: this.state.contactno,
            address: this.state.address
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/createstaff`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                name: '',
                email: '',
                password: '',
                cpassword: '',
                contactno: '',
                address: '',
                openform: false
            })
            this.getAllStaff()
        }).catch(error => console.error(`There was an error creating the ${this.state.name} user: ${error}`))
    }


    editStaff(data) {
        this.setState({
            openform: true,
            singleStaffArr: data,
            editStaff: true,
            name: data.name,
            email: data.email,
            contactno: data.contactno,
            address: data.address,
            password: data.password,
            cpassword: data.password
        })
    }

    updateStaff() {
        const dataObject = {
            idcode: this.state.singleStaffArr.idcode,
            name: this.state.name,
            email: this.state.email,
            password: this.state.password,
            contactno: this.state.contactno,
            address: this.state.address,
        }
        axios.post(`${apiService.apiUrl()}/users/updatestaff`, dataObject).then(response => {
            console.log(response.data)
            this.setState({
                name: '',
                email: '',
                password: '',
                cpassword: '',
                contactno: '',
                address: '',
                openform: false,
            })
            this.getAllStaff()
        })
            .catch(error => console.error(`There was an error removing the ${this.state.singleStaffArr.name} Staff: ${error}`))
    }

    addnewStaff() {
        this.setState({
            openform: !this.state.openform,
            name: '',
            email: '',
            password: '',
            cpassword: '',
            contactno: '',
            address: ''
        })
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="text-end mt-3">
                        {this.state.openform ?
                            <button className="btn btn-danger" onClick={this.addnewStaff}>Close</button> : <button className="btn btn-primary" onClick={this.addnewStaff}>Add</button>}
                    </div>
                    <div className="row mt-3">
                        <div className={this.state.openform ? "col-10" : "col-12"}>
                            <div>
                                <ViewStaffData data={this.state.allStaffArr} getAllStaff={this.getAllStaff} updateStaff={this.editStaff} />
                            </div>
                        </div>
                        {this.state.openform ?

                            <div className="col-2">
                                <div className="fieldset">
                                    <span className="legend">Add New</span>
                                    
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Name</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="name" value={this.state.name} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Email</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="email" value={this.state.email} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Password</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="password" value={this.state.password} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Confirm Password</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="cpassword" value={this.state.cpassword} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Conatact No.</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="contactno" value={this.state.contactno} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Address</label>
                                            <textarea type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="address" value={this.state.address} />
                                        </div>
                                    </div>

                                    <div className="mt-3">
                                        {this.state.editStaff ?
                                            <button className="btn btn-primary w-100" onClick={this.updateStaff}>Update</button> :
                                            <button className="btn btn-primary w-100" onClick={this.onClickSubmit}>Submit</button>}
                                    </div>
                                </div>
                            </div>
                            : ""}
                    </div>


                </div>
            </div>
        );
    }
}

export default Staff;