import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewCategoryData from './ViewCategoryData';

class Category extends Component {
    constructor(props) {
        super(props);
        this.state = {
            categoryname: '',
            allCategoryArr: '',
            editcategory: false
        }
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.getAllCategory = this.getAllCategory.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.editcategory = this.editcategory.bind(this)
        this.updateCategory = this.updateCategory.bind(this)
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    componentDidMount() {
        this.getAllCategory()
    }

    getAllCategory() {
        axios.get(`${apiService.apiUrl()}/users/categoryall`).then(response => {
            console.log(response.data);
            this.setState({
                allCategoryArr: response.data
            })
        })
    }

    onClickSubmit() {
        const dataObject = {
            categoryname: this.state.categoryname
        }
        axios.post(`${apiService.apiUrl()}/users/createcategory`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                categoryname: ''
            })
            this.getAllCategory()
        }).catch(error => console.error(`There was an error creating the ${this.state.name} user: ${error}`))
    }

    editcategory(data) {
        console.log(data);
        this.setState({
            singleCategory: data,
            editcategory: true,
            categoryname: data.categoryname
        })
    }

    updateCategory() {
        const dataObject = {
            idcode: this.state.singleCategory.idcode,
            categoryname: this.state.categoryname
        }
        axios.post(`${apiService.apiUrl()}/users/updatecategory`, dataObject).then(response => {
            console.log(response.data)
            this.setState({
                categoryname: ''
            })
            this.getAllCategory()
        })
            .catch(error => console.error(`There was an error removing the ${this.state.singleCategory.name} Staff: ${error}`))
    }

    render() {
        return (
            <div>
                <div className="container">

                    <div className="row">
                        <div className="col-2 mt-3">
                            <div className="fieldset">
                                <span className="legend">Categories</span>

                                <div className="mt-2">
                                    <div className="form-group">
                                        <label htmlFor="">Add New</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="categoryname" value={this.state.categoryname} placeholder="Category Name" />
                                    </div>
                                </div>

                                <div className="mt-2">
                                    {this.state.editcategory ?
                                        <button className="btn btn-primary w-100" onClick={this.updateCategory}>Update</button> :
                                        <button className="btn btn-primary w-100" onClick={this.onClickSubmit}>Add</button>}
                                </div>
                            </div>
                        </div>

                        <div className="col-10">

                            <ViewCategoryData data={this.state.allCategoryArr} getAllCategory={this.getAllCategory} editcategory={this.editcategory} />
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Category;