import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../ApiService/ApiService';

class Category extends Component {
    constructor(props) {
        super(props);
        this.state = {
            category: '',
            searchCategoryArr: [],
            searchCategory: false,
           
        }
        this.onChange = this.onChange.bind(this)
        this.onClickCategoryValue = this.onClickCategoryValue.bind(this)
    }

    onChange(e) {

        this.setState({
            [e.target.name]: e.target.value
        }, () => {
            if (e.target.name === "category") {
                if (this.state.category === "") {
                    this.setState({
                        searchCategory: false
                    })
                } else {
                    this.setState({
                        searchCategory: true
                    })
                    const dataObject = {
                        categoryname: this.state.category
                    }
                    axios.post(`${apiService.apiUrl()}/users/categoryexists`, dataObject).then(response => {
                        console.log(response.data);
                        this.setState({
                            searchCategoryArr: response.data
                        })
                    }).catch(error => console.error(`something went to wrong ${error}`))
                }


            } 
        })
    }


    onClickCategoryValue(e) {
        this.setState({
            category: e.target.id,
            searchCategory: false
        })

        this.props.categoryValue(e.target.id)
    }

    render() {
        return (
            <div>

                <div className="">
                    <div className="form-group">
                        <label htmlFor="">Category</label>
                        <input type="text" onChange={this.onChange}
                            className="form-control" name="category" value={this.state.category} />
                        {this.state.searchCategory ?
                            <div className="col-3 bg-white shadow" style={{ maxHeight: 100, position: "absolute", overflow: "auto" }}>
                                {this.state.searchCategoryArr.map((data, i) =>
                                    <div className="px-2" key={i}>
                                        <p className="mt-3" id={data.categoryname} onClick={this.onClickCategoryValue}>{data.categoryname}</p>
                                    </div>
                                )}
                            </div> : ""}
                    </div>
                   
                </div>
            </div>
        );
    }
}

export default Category;