import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewOrdersData from './ViewOrdersData';
import '../../StyleSheets/Style.scss'
import Category from '../../CommonComponents/Category';
import Product from '../../CommonComponents/Product';
class Orders extends Component {
    createProductData = []
    constructor(props) {
        super(props);
        this.state = {
            idcode: Date.now(),
            clienttype: '',
            clientid: '',
            status: '',
            allCustomerData: [],
            allVendorData: [],
            allCategoryArr: [],
            allProductArr: [],
            createProductData: [],
            allOrdersData: [],
            singleOrderDataArr: '',
            addproductform: false,
            category: '',
            product: '',
            qty: '',
            price: '',
            total: '',
            openAddForm: false,
            editForm: false,
            addproductbtn: false,
            editItem: false,
            dataofindex: '',
            singleItemData: ''
        }

        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.getAllCustomer = this.getAllCustomer.bind(this)
        this.getAllVendor = this.getAllVendor.bind(this)
        this.getAllCategory = this.getAllCategory.bind(this)
        this.getAllOrders = this.getAllOrders.bind(this)
        this.addProduct = this.addProduct.bind(this)
        this.createProduct = this.createProduct.bind(this)
        this.createOrder = this.createOrder.bind(this)
        this.addNewOrder = this.addNewOrder.bind(this)
        this.editOrders = this.editOrders.bind(this)
        this.viewOrders = this.viewOrders.bind(this)
        this.removeItem = this.removeItem.bind(this)
        this.updateOrder = this.updateOrder.bind(this)
        this.categoryValue = this.categoryValue.bind(this)
        this.productValue = this.productValue.bind(this)
        this.editItem = this.editItem.bind(this)
        this.updateItem = this.updateItem.bind(this)
        this.finalSubmit = this.finalSubmit.bind(this)
        this.searchOrderData = this.searchOrderData.bind(this)

    }

    componentDidMount() {
        this.getAllCustomer()
        this.getAllVendor()
        this.getAllOrders()

    }



    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        }, () => {
            if (e.target.name === "clienttype") {
                this.setState({
                    clientid: ''
                })
            }

            if (e.target.name === "category") {
                console.log(this.state.category);
                const dataobject = {
                    category: this.state.category
                }
                axios.post(`${apiService.apiUrl()}/users/getproductsbycategory`, dataobject).then(response => {
                    console.log(response.data);
                    this.setState({
                        allProductArr: response.data
                    })
                }).catch(error => console.error(`Something went to wrong ${error}`))

            }

            if (e.target.name === 'qty' || e.target.name === "price") {
                this.setState({
                    total: this.state.qty * this.state.price,
                })
            }

            if (this.state.editForm === true) {
                if (e.target.name === "status") {
                    // alert(1)
                    const dataObject = {
                        idcode: this.state.singleOrderDataArr.idcode,
                        status: this.state.status
                    }
                    axios.post(`${apiService.apiUrl()}/users/updateorders`, dataObject).then(response => {
                        console.log(response.data);
                        alert(response.data.message)
                    })
                }
            }

        })
    }

    addProduct() {
        this.getAllCategory()
        this.setState({
            addproductform: !this.state.addproductform,
            category: '',
            product: '',
            qty: '',
            price: '',
            total: '',
        })
    }

    getAllCustomer() {
        axios.get(`${apiService.apiUrl()}/users/customerall`).then(response => {
            console.log(response.data)
            this.setState({
                allCustomerData: response.data
            })

        })
            .catch(error => console.error(`There was an error retrieving the book list: ${error}`))
    }

    getAllVendor() {
        axios.get(`${apiService.apiUrl()}/users/vendorsall`).then(response => {
            console.log(response.data);
            this.setState({
                allVendorData: response.data
            })
        }).catch(error => console.error(`something went to wrong ${error}`))
    }



    getAllCategory() {
        axios.get(`${apiService.apiUrl()}/users/categoryall`).then(response => {
            console.log(response.data);
            this.setState({
                allCategoryArr: response.data
            })
        })
    }

    getAllOrders() {
        axios.get(`${apiService.apiUrl()}/users/ordersall`).then(response => {
            console.log(response.data);
            this.setState({
                allOrdersData: response.data
            })
        }).catch(error => console.error(`something went to wrong ${error}`))
    }



    categoryValue(data) {
        // alert(1)
        console.log(data);
        this.setState({
            category: data
        })
    }
    productValue(data) {
        console.log(data);
        this.setState({
            product: data
        })
    }

    createProduct() {
        const dataobject = {
            orderid: this.state.idcode,
            category: this.state.category,
            product: this.state.product,
            qty: this.state.qty,
            price: this.state.price,
            total: this.state.qty * this.state.price,
        }
        console.log(dataobject);
        axios.post(`${apiService.apiUrl()}/users/createsuborders`, dataobject).then(response => {
            console.log(response.data);
            if (response.data) {
                const dataObject = {
                    orderid: this.state.idcode
                }
                console.log(dataObject);
                axios.post(`${apiService.apiUrl()}/users/getproductsbyorderid`, dataObject).then(response => {
                    console.log(response.data);

                    var itemsDataArr = response.data
                    this.setState({
                        createProductData: itemsDataArr,
                        addproductform: false,

                    })
                })

            }
        })
    }

    createOrder() {
        const dataObject = {
            idcode: this.state.idcode,
            orderdate: new Date(),
            clienttype: this.state.clienttype,
            clientid: this.state.clientid,
            status: this.state.status,
        }
        axios.post(`${apiService.apiUrl()}/users/createorders`, dataObject).then(response => {
            console.log(response.data.data);
            if (response.data.data) {


                this.setState({
                    addproductform: true,
                    addproductbtn: true
                })

            } else {
                console.log(response.data.error);
            }

        }).catch(error => console.error(console.error(`Something went to wrong ${error}`)))
    }

    addNewOrder() {
        this.setState({
            editForm: false,
            addproductbtn: false,
            openAddForm: !this.state.openAddForm,
            createProductData: [],
            idcode: Date.now(),
            clienttype: '',
            clientid: '',
            status: '',
        })
        this.getAllOrders()
    }

    editOrders(data) {

        console.log(data);
        const dataObject = {
            orderid: data.idcode
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/getproductsbyorderid`, dataObject).then(response => {
            console.log(response.data);

            var itemsDataArr = response.data

            this.setState({
                addproductbtn: true,
                singleOrderDataArr: data,
                editForm: true,
                openAddForm: true,
                addproductform: false,
                createProductData: itemsDataArr,
                idcode: data.idcode,
                orderdate: data.orderdate,
                clienttype: data.clienttype,
                clientid: data.clientid,
                status: data.status,
            })
        })

    }

    updateOrder() {
        const dataObject = {
            idcode: this.state.singleOrderDataArr.idcode,
            orderdate: this.state.orderdate,
            clienttype: this.state.clienttype,
            clientid: this.state.clientid,
            status: this.state.status,
        }
        console.log(dataObject);
    }

    viewOrders(e) {
        this.props.history.push(e.url)
    }

    removeItem(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/Salesdelete`, dataObject).then(() => {
            console.log(`Sales ${e.idcode} removed.`)
            const dataObject = {
                orderid: this.state.singleOrderDataArr.idcode
            }
            console.log(dataObject);
            axios.post(`${apiService.apiUrl()}/users/getproductsbyorderid`, dataObject).then(response => {
                console.log(response.data);

                var itemsDataArr = response.data
                this.setState({
                    createProductData: itemsDataArr
                })
            })
        })
            .catch(error => console.error(`There was an error removing the ${e.idcode} Staff: ${error}`))

    }

    editItem(e) {
        console.log(e);
        this.setState({
            singleItemData: e,
            dataofindex: e.idcode,
            editItem: true,
            category: e.category,
            product: e.product,
            qty: e.qty,
            price: e.price,
            total: e.total
        })
    }


    updateItem() {
        const dataObject = {
            idcode: this.state.singleItemData.idcode,
            qty: this.state.qty,
            price: this.state.price,
            total: this.state.qty * this.state.price
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/updatesuborders`, dataObject).then(response => {
            console.log(response.data);
            if (response.data) {
                const dataObject = {
                    orderid: this.state.singleOrderDataArr.idcode
                }
                console.log(dataObject);
                axios.post(`${apiService.apiUrl()}/users/getproductsbyorderid`, dataObject).then(response => {
                    console.log(response.data);

                    var itemsDataArr = response.data
                    this.setState({
                        createProductData: itemsDataArr
                    })
                })
            }
            this.setState({
                dataofindex: ''

            })
        })

    }


    
    finalSubmit() {
        this.setState({
            openAddForm: false,
            idcode: Date.now(),
            clienttype: '',
            clientid: '',
            status: '',
            singleOrderDataArr: '',
            addproductform: false,
            category: '',
            product: '',
            qty: '',
            price: '',
            total: '',
        })
        this.getAllOrders()
    }

    searchOrderData(data){
        this.setState({
            allOrdersData:data
        })
    }


    render() {
        return (
            <div>
                <div className="container">
                    <div className="text-end">
                        {this.state.openAddForm ?
                            <button className="btn btn-danger" onClick={this.addNewOrder}>Close</button> :
                            <button className="btn btn-primary" onClick={this.addNewOrder}>Add</button>}
                    </div>

                    {this.state.openAddForm ?
                        <div className="mt-3">

                            <div className="fieldset">
                                <span className="legend">Add New</span>

                                <div className="row m-0 py-3">
                                    <div className="col-3">
                                        <div className="form-group">
                                            <label htmlFor="">Order No.</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="idcode" value={this.state.idcode} readOnly />
                                        </div>
                                    </div>

                                    <div className="col-3">
                                        <div className="form-group">
                                            <label htmlFor="">Order Type</label>
                                            <select type="text" onChange={this.onChangeHandle}
                                                className="form-select" name="clienttype" value={this.state.clienttype} disabled={this.state.editForm ? true : false}>
                                                <option value="">Select</option>
                                                <option value="Sale">Sale</option>
                                                <option value="Purchase">Purchase</option>
                                            </select>
                                        </div>
                                    </div>

                                    {this.state.clienttype === "Sale" ?
                                        <div className="col-3">
                                            <div className="form-group">
                                                <label htmlFor="">Customer Name</label>
                                                <select type="text" onChange={this.onChangeHandle}
                                                    className="form-select" name="clientid" value={this.state.clientid} disabled={this.state.editForm ? true : false}>
                                                    <option value="">Select</option>
                                                    {this.state.allCustomerData.map((data, i) =>
                                                        <option key={i} value={data.idcode}>{data.name} ({data.idcode})</option>
                                                    )}
                                                </select>
                                            </div>
                                        </div>
                                        :
                                        this.state.clienttype === "Purchase" ?
                                            <div className="col-3">
                                                <div className="form-group">
                                                    <label htmlFor="">Vendor Name</label>
                                                    <select type="text" onChange={this.onChangeHandle}
                                                        className="form-control" name="clientid" value={this.state.clientid} >
                                                        <option value="">Select</option>
                                                        {this.state.allVendorData.map((data, i) =>
                                                            <option key={i} value={data.idcode}>{data.name} ({data.idcode})</option>
                                                        )}
                                                    </select>
                                                </div>
                                            </div> : ""}

                                    <div className="col-3">
                                        <div className="form-group">
                                            <label htmlFor="">Status</label>
                                            <select type="text" onChange={this.onChangeHandle}
                                                className="form-select" name="status" value={this.state.status} >
                                                <option value="">Select</option>
                                                <option value="Pending">Pending</option>
                                                <option value="In Process">In Process</option>
                                                <option value="Completed">Completed</option>
                                            </select>
                                        </div>
                                    </div>

                                    {this.state.addproductbtn ? '' :
                                        <div className="mt-3">
                                            {(this.state.clienttype === "" || this.state.clientid === "" || this.state.status === "" || this.state.addproductform === true) && (this.createProductData.length === 0 || this.state.category === "" || this.state.product === "" || this.state.qty === "" || this.state.price === "") ?
                                                <button className="btn btn-primary" disabled>Create Order</button> :
                                                <button className="btn btn-primary" onClick={this.createOrder}>Create Order</button>}

                                        </div>}

                                    <div className="mt-2 text-end">
                                        {this.state.addproductbtn ?
                                            <div>
                                                {this.state.addproductform ? "" :
                                                    <span className="pointer text-primary" onClick={this.addProduct}>+ Add Product</span>}
                                            </div> : ''}
                                    </div>



                                    {this.state.createProductData.length > 0 ?
                                        <div>
                                            <div className="fw-bold my-3 border-bottom">
                                                <div className="row m-0 py-2">
                                                    <div className="col">Category Name</div>
                                                    <div className="col">Product Name</div>
                                                    <div className="col">Quantity</div>
                                                    <div className="col">Price Per/pcs</div>
                                                    <div className="col">Total</div>
                                                    <div className="col"></div>
                                                </div>
                                            </div>
                                            {this.state.createProductData.map((data, i) =>
                                                <div key={i}>
                                                    <div className="row m-0 p-2">
                                                        <div className="col">{data.category}</div>
                                                        <div className="col">{data.product}</div>
                                                        {this.state.dataofindex === data.idcode ? <div className="col">
                                                            <input className="form-control m-0 m-0" name="qty" value={this.state.qty} onChange={this.onChangeHandle} />
                                                        </div> :
                                                            <div className="col">{data.qty}</div>}
                                                        {this.state.dataofindex === data.idcode ? <div className="col">
                                                            <input className="form-control m-0" name="price" value={this.state.price} onChange={this.onChangeHandle} />
                                                        </div> :
                                                            <div className="col">{data.price}</div>}

                                                        {this.state.dataofindex === data.idcode ?
                                                            <div className="col">{this.state.total}</div> :
                                                            <div className="col">{data.total}</div>}
                                                        <div className="col">
                                                            {this.state.editForm ? <span className="col">
                                                                {this.state.dataofindex === data.idcode ?
                                                                    <span className="text-primary me-2 pointer" onClick={this.updateItem}>Save</span> :
                                                                    <span className=" text-primary me-2 pointer" onClick={() => this.editItem(data)}>Edit</span>}
                                                            </span> : ''}

                                                            <span className="col text-danger ms-2 pointer" onClick={() => this.removeItem(data)}>Remove</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            )}
                                        </div> : ''}







                                    {this.state.addproductform ?
                                        <div className="mt-3">
                                            <div className="text-end">
                                                <span className="pointer text-danger" onClick={this.addProduct}>X close</span>
                                            </div>

                                            <div className="row m-0">
                                                <div className="col-2">
                                                    <Category categoryValue={this.categoryValue} />
                                                    {/* <div className="form-group">
                                                <label htmlFor="">Category</label>
                                                <select onChange={this.onChangeHandle}
                                                    className="form-select" name="category" value={this.state.category} >
                                                    <option value="">Select</option>
                                                    {this.state.allCategoryArr.map((data, i) =>
                                                        <option key={i} value={data.categoryname}>{data.categoryname}</option>
                                                    )}
                                                </select>
                                            </div> */}
                                                </div>

                                                <div className="col-2">
                                                    <Product productValue={this.productValue} />
                                                    {/* <div className="form-group">
                                                <label htmlFor="">Product</label>
                                                <select onChange={this.onChangeHandle}
                                                    className="form-select" name="product" value={this.state.product} >
                                                    <option value="">Select</option>
                                                    {this.state.allProductArr.map((data, i) =>
                                                        <option key={i} value={data.productname}>{data.productname}</option>
                                                    )}
                                                </select>
                                            </div> */}
                                                </div>

                                                <div className="col-2">
                                                    <div className="form-group">
                                                        <label htmlFor="">Quantity</label>
                                                        <input type="text" onChange={this.onChangeHandle}
                                                            className="form-control" name="qty" value={this.state.qty} />
                                                    </div>
                                                </div>

                                                <div className="col-2">
                                                    <div className="form-group">
                                                        <label htmlFor="">Price</label>
                                                        <input type="text" onChange={this.onChangeHandle}
                                                            className="form-control" name="price" value={this.state.price} />
                                                    </div>
                                                </div>

                                                <div className="col-2">
                                                    <div className="form-group">
                                                        <label htmlFor="">Total</label>
                                                        <input type="text" onChange={this.onChangeHandle}
                                                            className="form-control" name="total" value={this.state.total} readOnly />
                                                    </div>
                                                </div>

                                                <div className="col-2 mt-4">
                                                    <button className="btn btn-primary w-100" onClick={this.createProduct}>Add</button>
                                                </div>
                                            </div>

                                        </div> : ''}

                                    {this.state.addproductbtn ?
                                        <div className="mt-2" >
                                            <button className="btn btn-primary" onClick={this.finalSubmit}>Done</button>
                                        </div> : ''}
                                </div>
                            </div>
                        </div> :

                        <div>
                            <ViewOrdersData data={this.state.allOrdersData} viewOrders={this.viewOrders} getAllOrders={this.getAllOrders} editOrders={this.editOrders} searchOrderData={this.searchOrderData}/>
                        </div>}

                </div>

            </div>
        );
    }
}

export default Orders;