import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../ApiService/ApiService';
import '../StyleSheets/Style.scss'

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
        }

        this.onChnageHandle = this.onChnageHandle.bind(this)
        this.onClickLogin = this.onClickLogin.bind(this)
    }
    onChnageHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onClickLogin() {
        const dataObject = {
            username: this.state.username,
            password: this.state.password
        }
        console.log(dataObject);

        axios.post(`${apiService.apiUrl()}/users/login`, dataObject)
            .then(response => {
                if (response.data.length > 0) {
                    console.log(response.data);
                    this.props.history.push("/dashboard/")
                } else {
                    alert('invalid username password')
                }
            })
            .catch(error => console.error(`There was an error creating the ${this.state.name} user: ${error}`))
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="login-container shadow rounded">
                        <div className="fs-5 p-1 rounded  fw-bold text-center shadow ">Admin Login</div>
                        <div className="p-3">
                            <div className="mt-3">
                                <div className="form-group shadow">
                                    <input type="text" onChange={this.onChnageHandle}
                                        className="form-control border-0" name="username" value={this.state.username} placeholder="Username" />
                                </div>
                            </div>
                            <div className="mt-3 shadow">
                                <div className="form-group">
                                    <input type="password" onChange={this.onChnageHandle}
                                        className="form-control border-0" name="password" value={this.state.password} placeholder="Password" />
                                </div>
                            </div>

                            <div className="mt-3">
                                <button className="btn shadow" onClick={this.onClickLogin}>Login</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Login;