import axios from 'axios';
import React, { Component } from 'react';
import { Modal, ModalBody } from 'react-bootstrap';
import ModalHeader from 'react-bootstrap/esm/ModalHeader';
import apiService from '../../ApiService/ApiService';
import ViewCustomer from './ViewCustomer';

class Customers extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idcode: '',
            name: '',
            emailid: '',
            contactno: '',
            address: '',
            reference: '',
            refcontactno: '',
            status: 'active',
            picture: '',
            documents: '',
            addNewCustomer: false,
            customerArr: [],
            formTabs: 'info',
            pictureUploaded: false,
            documentsUploaded: false,
            editForm: false,
            addref: false,
            refname: '',
            refmobileno: '',
            refaddress: '',
            refnotes: '',
            editpic: false,
            editdoc: false
        }

        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.pictureSelect = this.pictureSelect.bind(this)
        this.documentSelect = this.documentSelect.bind(this)
        this.onClickAddNewCustomer = this.onClickAddNewCustomer.bind(this)
        this.closeBtn = this.closeBtn.bind(this)
        this.onClickinfoSubmit = this.onClickinfoSubmit.bind(this)
        this.onClickPictureSubmit = this.onClickPictureSubmit.bind(this)
        this.onClickDocumentSubmit = this.onClickDocumentSubmit.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.getAllCustomerData = this.getAllCustomerData.bind(this)
        this.updateData = this.updateData.bind(this)
        this.updateCustomerInfo = this.updateCustomerInfo.bind(this)
        this.addNewRef = this.addNewRef.bind(this)
        this.submitrefinfo = this.submitrefinfo.bind(this)
        this.editPicture = this.editPicture.bind(this)
        this.editDocument = this.editDocument.bind(this)
    }

    componentDidMount() {
        this.getAllCustomerData()
    }

    getAllCustomerData() {
        axios.get(`${apiService.apiUrl()}/users/customerall`).then(response => {
            console.log(response.data);
            this.setState({
                customerArr: response.data
            })
        }).catch(error => { console.error(`Something went to wrong ${error}`) })
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }



    onClickAddNewCustomer() {
        this.setState({
            addNewCustomer: true
        })
    }
    closeBtn() {
        this.setState({
            idcode: '',
            name: '',
            emailid: '',
            contactno: '',
            address: '',
            reference: '',
            refcontactno: '',
            status: 'active',
            picture: '',
            documents: '',
            addNewCustomer: false,
            formTabs: 'info',
            pictureUploaded: false,
            documentsUploaded: false,
            editForm: false,
            editpic: false,
            editdoc: false
        })
    }
    pictureSelect(e) {
        this.setState({
            picture: e.target.files[0]
        })
    }

    documentSelect(e) {
        console.log(e.target.files[0]);
        this.setState({
            documents: e.target.files[0]
        })
    }

    onClickinfoSubmit() {
        console.log('info');
        var id = Date.now()
        console.log(id);
        this.setState({
            formTabs: 'documents',
            idcode: id
        }, () => {
            const dataObject = {
                idcode: this.state.idcode,
                name: this.state.name,
                emailid: this.state.emailid,
                contactno: this.state.contactno,
                address: this.state.address,
                reference: this.state.reference,
                refcontactno: this.state.refcontactno,
                status: this.state.status
            }
            console.log(dataObject);
            axios.post(`${apiService.apiUrl()}/users/createcustomer`, dataObject).then(response => {
                console.log(response.data);
            }).catch(error => { console.error(`Something went to wrong ${error}`) })
        })

    }

    onClickPictureSubmit() {
        console.log('picture');
        var formData = new FormData()
        formData.append('picture', this.state.picture)
        formData.append('idcode', this.state.idcode)

        console.log(formData);
        console.log(this.state.idcode);
        axios.post(`${apiService.apiUrl()}/users/pictureupload`, formData).then(response => {
            console.log(response.data);
            this.setState({
                pictureUploaded: true
            })
        }).catch(error => { console.error(`Something went to wrong ${error}`) })
    }

    onClickDocumentSubmit() {
        console.log('document');
        var formData = new FormData()
        formData.append('documents', this.state.documents)
        formData.append('idcode', this.state.idcode)

        console.log(formData);
        console.log(this.state.idcode);

        axios.post(`${apiService.apiUrl()}/users/documentupload`, formData).then(response => {
            console.log(response.data);
            this.setState({
                documentsUploaded: true
            })
        }).catch(error => { console.error(`Something went to wrong ${error}`) })


    }

    onClickSubmit() {
        this.closeBtn()
        this.getAllCustomerData()
    }

    updateData(data) {
        console.log(data);
        this.setState({
            editForm: true,
            pictureUploaded: true,
            documentsUploaded: true,
            addNewCustomer: true,
            idcode: data.idcode,
            name: data.name,
            emailid: data.emailid,
            contactno: data.contactno,
            address: data.address,
            reference: data.reference,
            refcontactno: data.refcontactno,
            status: data.status,
            picture: data.picture,
            documents: data.documents
        })
    }

    updateCustomerInfo() {
        const dataObject = {
            idcode: this.state.idcode,
            name: this.state.name,
            emailid: this.state.emailid,
            contactno: this.state.contactno,
            address: this.state.address,
            reference: this.state.reference,
            refcontactno: this.state.refcontactno,
            status: this.state.status
        }
        axios.post(`${apiService.apiUrl()}/users/updatecustomer`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                formTabs: 'documents',
                // addNewCustomer: false
            })
        }).catch(error => { console.error(`Something went to wrong ${error}`) })
    }

    addNewRef() {
        this.setState({
            addref: !this.state.addref
        })
    }

    submitrefinfo() {
        const dataObject = {
            name: this.state.refname,
            contactno: this.state.refmobileno,
            address: this.state.refaddress,
            notes: this.state.refnotes,
        }
        axios.post(`${apiService.apiUrl()}/users/createreference`, dataObject).then(response => {
            console.log(response.data);
            this.addNewRef()
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    editPicture() {
        this.setState({
            editpic: !this.state.editpic,
            picture: '',
            pictureUploaded: false
        })
    }
    editDocument() {
        this.setState({
            editdoc: !this.state.editdoc,
            documents: '',
            documentsUploaded: false
        })
    }

    render() {
        return (
            <div>
                <Modal show={this.state.addref} onHide={this.addNewRef}>
                    <ModalHeader>
                        <div className="">
                            <div className="fs-5 badge bg-secondary">Add New Reference</div>
                        </div>
                    </ModalHeader>
                    <ModalBody>

                        <div>
                            <div className="">
                                <div className="form-group">
                                    <label htmlFor="">Name</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="refname" value={this.state.refname} />
                                </div>
                            </div>

                            <div className="">
                                <div className="form-group">
                                    <label htmlFor="">Contact No.</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="refmobileno" value={this.state.refmobileno} />
                                </div>
                            </div>

                            <div className="">
                                <div className="form-group">
                                    <label htmlFor="">Address</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="refaddress" value={this.state.refaddress} />
                                </div>
                            </div>

                            <div className="">
                                <div className="form-group">
                                    <label htmlFor="">Notes</label>
                                    <textarea type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="refnotes" value={this.state.refnotes} />
                                </div>
                            </div>

                            <div className="my-3">
                                <button className="btn btn-primary" onClick={this.submitrefinfo}>Submit</button>
                            </div>
                        </div>
                    </ModalBody>
                </Modal>
                <div className="container">
                    <div className="text-end">
                        {this.state.addNewCustomer ?
                            <button className="btn btn-danger" onClick={this.closeBtn}>Close</button> :
                            <button className="btn btn-primary" onClick={this.onClickAddNewCustomer}>Add New</button>}
                    </div>

                    {this.state.addNewCustomer ?
                        <div className="mt-3">
                            <div className="fieldset">
                                <span className="legend">Add New</span>


                                {this.state.formTabs === 'info' ?
                                    <div className="row">
                                        <div className="col-4">
                                            <div className="form-group">
                                                <label htmlFor="">Name</label>
                                                <input type="text" onChange={this.onChangeHandle}
                                                    className="form-control" name="name" value={this.state.name} />
                                            </div>
                                        </div>

                                        <div className="col-4">
                                            <div className="form-group">
                                                <label htmlFor="">Email id</label>
                                                <input type="text" onChange={this.onChangeHandle}
                                                    className="form-control" name="emailid" value={this.state.emailid} />
                                            </div>
                                        </div>

                                        <div className="col-4">
                                            <div className="form-group">
                                                <label htmlFor="">Mobile No.</label>
                                                <input type="text" onChange={this.onChangeHandle}
                                                    className="form-control" name="contactno" value={this.state.contactno} />
                                            </div>
                                        </div>

                                        <div className="col-4">
                                            <div className="form-group">
                                                <label htmlFor="">Reference</label>
                                                <input type="text" onChange={this.onChangeHandle}
                                                    className="form-control" name="reference" value={this.state.reference} />
                                                <div className="text-end pointer" style={{ fontSize: "11px" }}>If you don't get Reference?
                                                    <span className="text-primary" onClick={this.addNewRef}>Click Here.</span></div>
                                            </div>
                                        </div>

                                        <div className="col-4">
                                            <div className="form-group">
                                                <label htmlFor="">Ref. Contact No.</label>
                                                <input type="text" onChange={this.onChangeHandle}
                                                    className="form-control" name="refcontactno" value={this.state.refcontactno} />
                                            </div>
                                        </div>

                                        <div className="col-4">
                                            <div className="form-group">
                                                <label htmlFor="">Status</label>
                                                <select onChange={this.onChangeHandle}
                                                    className="form-select" name="status" value={this.state.status} >
                                                    <option value="active">Active</option>
                                                    <option value="inactive">Inactive</option>
                                                </select>
                                            </div>
                                        </div>

                                        <div className="col-12">
                                            <div className="form-group">
                                                <label htmlFor="">Address</label>
                                                <textarea type="text" onChange={this.onChangeHandle}
                                                    className="form-control" name="address" value={this.state.address} />
                                            </div>
                                        </div>

                                        <div className="mt-3">
                                            {this.state.editForm ?
                                                <button className="btn btn-primary" id='documents' onClick={this.updateCustomerInfo}>Update</button> :
                                                <button className="btn btn-primary" id='documents' onClick={this.onClickinfoSubmit}>Next</button>}
                                        </div>
                                    </div> :

                                    this.state.formTabs === 'documents' ?

                                        <div>
                                            <div className="row">
                                                <div className="col-6">
                                                    {this.state.editForm ?
                                                        <div>
                                                            {this.state.editpic ? <div>
                                                                <div>
                                                                    <div className="form-group">
                                                                        <label htmlFor="">Picture</label>
                                                                        <input className="form-control" type="file" name="picture" onChange={this.pictureSelect} />
                                                                    </div>
                                                                </div>

                                                                <div className="mt-3 ">
                                                                    {this.state.picture === '' || this.state.picture === null ? <div>Please setelect image file</div> :
                                                                        <div>
                                                                            <img src={URL.createObjectURL(this.state.picture)} width="200" height="200" alt="" />
                                                                            <div className="mt-3">
                                                                                {this.state.pictureUploaded ? '' :
                                                                                    <button className="btn btn-primary" id='document' onClick={this.onClickPictureSubmit}>Upload</button>}
                                                                            </div>
                                                                        </div>}
                                                                </div>


                                                            </div> :
                                                                <div>
                                                                    <div>Picture</div>
                                                                    <div className="btn btn-primary" onClick={this.editPicture}>edit</div>
                                                                    <div className="mt-3 ">
                                                                        {this.state.picture === '' || this.state.picture === null ? <div>Please setelect image file</div> :
                                                                            <div>
                                                                                <img src={`${apiService.apiUrl()}/users/pictures/${this.state.picture}`} width="200" height="200" alt="" />
                                                                            </div>}
                                                                    </div>
                                                                </div>}
                                                        </div>
                                                        :
                                                        <div>
                                                            <div>
                                                                <div className="form-group">
                                                                    <label htmlFor="">Picture</label>
                                                                    <input className="form-control" type="file" name="picture" onChange={this.pictureSelect} />
                                                                </div>
                                                            </div>

                                                            <div className="mt-3 ">
                                                                {this.state.picture === '' || this.state.picture === null ? <div>Please setelect image file</div> :
                                                                    <div>
                                                                        <img src={URL.createObjectURL(this.state.picture)} width="200" height="200" alt="" />
                                                                        <div className="mt-3">
                                                                            {this.state.pictureUploaded ? '' :
                                                                                <button className="btn btn-primary" id='document' onClick={this.onClickPictureSubmit}>Upload</button>}
                                                                        </div>
                                                                    </div>}
                                                            </div>


                                                        </div>}
                                                </div>


                                                <div className="col-6">
                                                    {this.state.editForm ?
                                                        <div>
                                                            {this.state.editdoc ? <div>
                                                                <div>
                                                                    <div className="form-group">
                                                                        <label htmlFor="">Documents</label>
                                                                        <input className="form-control" type="file" name="documents" onChange={this.documentSelect} />
                                                                    </div>
                                                                </div>

                                                                <div className="mt-3 ">
                                                                    {this.state.documents === '' || this.state.documents === null ? <div>Please setelect document file</div> :
                                                                        <div>
                                                                            <img src={URL.createObjectURL(this.state.documents)} width="200" height="200" alt="" />
                                                                            <div className="mt-3">
                                                                                {this.state.documentsUploaded ? '' :
                                                                                    <button className="btn btn-primary" onClick={this.onClickDocumentSubmit}>Upload</button>}
                                                                            </div>
                                                                        </div>}
                                                                </div>


                                                            </div> :
                                                                <div>
                                                                    <div>Documents</div>
                                                                    <div className="btn btn-primary" onClick={this.editDocument}>edit</div>
                                                                    <div className="mt-3 ">
                                                                        {this.state.documents === '' || this.state.documents === null ? <div>Please setelect document file</div> :
                                                                            <div>
                                                                                <img src={`${apiService.apiUrl()}/users/documents/${this.state.documents}`} width="200" height="200" alt="" /> :
                                                                            </div>}
                                                                    </div>
                                                                </div>}
                                                        </div>
                                                        :
                                                        <div>
                                                            <div>
                                                                <div className="form-group">
                                                                    <label htmlFor="">Documents</label>
                                                                    <input className="form-control" type="file" name="documents" onChange={this.documentSelect} />
                                                                </div>
                                                            </div>

                                                            <div className="mt-3 ">
                                                                {this.state.documents === '' || this.state.documents === null ? <div>Please setelect document file</div> :
                                                                    <div>
                                                                        <img src={URL.createObjectURL(this.state.documents)} width="200" height="200" alt="" />
                                                                        <div className="mt-3">
                                                                            {this.state.documentsUploaded ? '' :
                                                                                <button className="btn btn-primary" onClick={this.onClickDocumentSubmit}>Upload</button>}
                                                                        </div>
                                                                    </div>}
                                                            </div>


                                                        </div>}
                                                </div>

                                            </div>
                                            <div className="mt-3 text-end">
                                                {this.state.pictureUploaded && this.state.documentsUploaded ?
                                                    <button className="btn btn-primary" onClick={this.onClickSubmit}>Submit</button> :
                                                    <button className="btn btn-primary" disabled>Submit</button>}
                                            </div>
                                        </div>

                                        : ''}

                            </div>
                        </div> :
                        <div>
                            {this.state.customerArr.length > 0 ? <div>
                                <ViewCustomer data={this.state.customerArr} getAllCustomerData={this.getAllCustomerData} updateData={this.updateData} />
                            </div> : <div>Customer data Not available</div>}
                        </div>}




                </div>
            </div>
        );
    }
}

export default Customers;