import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewParcelData from './ViewParcelData';

class Parcels extends Component {
    constructor(props) {
        super(props);
        this.state = {
            orderno: '',
            packingstatus: '',
            packingdate: '',
            packagesize: '',
            packageweight: '',
            packagecontent: '',
            shipmentname: '',
            toaddress: '',
            shipmentstatus: '',
            shipmentcode: '',
            customername: '',
            customercontactno: '',
            fromaddress: '',
            statusupdatedate: new Date(),
            comments: '',
            shipmentServices: [],
            allPercelsData: [],
            singleparcelsData: '',
            openform: false,
            editData: false,
        }
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.getAllShipmentsData = this.getAllShipmentsData.bind(this)
        this.getAllParcelsData = this.getAllParcelsData.bind(this)
        this.defaultValue = this.defaultValue.bind(this)
        this.editParcels = this.editParcels.bind(this)
        this.onClickAdd = this.onClickAdd.bind(this)
        this.onClickUpdate = this.onClickUpdate.bind(this)
        this.viewParcels = this.viewParcels.bind(this)
    }

    defaultValue() {
        this.setState({
            orderno: '',
            packingstatus: '',
            packingdate: '',
            packagesize: '',
            packageweight: '',
            packagecontent: '',
            shipmentname: '',
            toaddress: '',
            shipmentstatus: '',
            shipmentcode: '',
            customername: '',
            customercontactno: '',
            fromaddress: '',
            statusupdatedate: new Date(),
            comments: '',
        })
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    componentDidMount() {
        this.getAllParcelsData()
        this.getAllShipmentsData()
    }

    getAllParcelsData() {
        axios.get(`${apiService.apiUrl()}/users/parcelsall`).then(response => {
            console.log(response.data);
            this.setState({
                allPercelsData: response.data
            })
        })
    }
    getAllShipmentsData() {
        axios.get(`${apiService.apiUrl()}/users/shipmentserviceall`).then(response => {
            console.log(response.data);
            this.setState({
                shipmentServices: response.data
            })
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    onClickSubmit() {
        const dataObject = {
            orderno: this.state.orderno,
            packingstatus: this.state.packingstatus,
            packingdate: this.state.packingdate,
            packagesize: this.state.packagesize,
            packageweight: this.state.packageweight,
            packagecontent: this.state.packagecontent,
            shipmentname: this.state.shipmentname,
            toaddress: this.state.toaddress,
            shipmentstatus: this.state.shipmentstatus,
            shipmentcode: this.state.shipmentcode,
            customername: this.state.customername,
            customercontactno: this.state.customercontactno,
            fromaddress: this.state.fromaddress,
            statusupdatedate: this.state.statusupdatedate,
            comments: this.state.comments,
        }
        console.log(dataObject);

        axios.post(`${apiService.apiUrl()}/users/createparcels`, dataObject).then(response => {
            console.log(response.data);
            this.defaultValue()
            this.setState({
                openform: false
            })
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    onClickAdd() {
        this.setState({
            openform: !this.state.openform,
            editData: false
        })
        this.defaultValue()
    }

    editParcels(data) {

        this.setState({
            singleparcelsData: data,
            openform: true,
            editData: true,
            orderno: data.orderno,
            packingstatus: data.packingstatus,
            packingdate: data.packingdate,
            packagesize: data.packagesize,
            packageweight: data.packageweight,
            packagecontent: data.packagecontent,
            shipmentname: data.shipmentname,
            toaddress: data.toaddress,
            shipmentstatus: data.shipmentstatus,
            shipmentcode: data.shipmentcode,
            customername: data.customername,
            customercontactno: data.customercontactno,
            fromaddress: data.fromaddress,
            statusupdatedate: new Date(),
            comments: data.comments,
        })
    }

    onClickUpdate() {
        const dataObject = {
            idcode: this.state.singleparcelsData.idcode,
            orderno: this.state.orderno,
            packingstatus: this.state.packingstatus,
            packingdate: this.state.packingdate,
            packagesize: this.state.packagesize,
            packageweight: this.state.packageweight,
            packagecontent: this.state.packagecontent,
            shipmentname: this.state.shipmentname,
            toaddress: this.state.toaddress,
            shipmentstatus: this.state.shipmentstatus,
            shipmentcode: this.state.shipmentcode,
            customername: this.state.customername,
            customercontactno: this.state.customercontactno,
            fromaddress: this.state.fromaddress,
            statusupdatedate: this.state.statusupdatedate,
            comments: this.state.comments,
        }
        console.log(dataObject);

        axios.post(`${apiService.apiUrl()}/users/updateparcels`, dataObject).then(response => {
            console.log(response.data);
            this.defaultValue()
            this.setState({
                openform: false
            })
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    viewParcels(e) {
        this.props.history.push(e.url)
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div>
                        <div className="text-end">
                            {this.state.openform ?
                                <button className="btn btn-danger" onClick={this.onClickAdd}>Close</button> :
                                <button className="btn btn-primary" onClick={this.onClickAdd}>Add</button>}
                        </div>
                    </div>
                    {this.state.openform ?
                        <div className="mt-3">

                            <div className="fieldset">
                                <span className="legend">Add New</span>
                                <div className="row m-0">
                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Order No.</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="orderno" value={this.state.orderno} />
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Packing Status</label>
                                            <select onChange={this.onChangeHandle}
                                                className="form-select" name="packingstatus" value={this.state.packingstatus}>
                                                <option value="">Select</option>
                                                <option value="Pending">Pending</option>
                                                <option value="In Process">In Process</option>
                                                <option value="In Shipment">In Shipment</option>
                                                <option value="Delivered">Delivered</option>
                                            </select>
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Packing Date</label>
                                            <input type="date" onChange={this.onChangeHandle}
                                                className="form-control" name="packingdate" value={this.state.packingdate} />
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Package Size</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="packagesize" value={this.state.packagesize} />
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Package Weight</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="packageweight" value={this.state.packageweight} />
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Package Content</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="packagecontent" value={this.state.packagecontent} />
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Shipment Code </label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="shipmentcode" value={this.state.shipmentcode} />
                                        </div>
                                    </div>

                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Shipment Status</label>
                                            <select onChange={this.onChangeHandle}
                                                className="form-select" name="shipmentstatus" value={this.state.shipmentstatus}>
                                                <option value="">Select</option>
                                                <option value="Pending">Pending</option>
                                                <option value="In Shipment">In Shipment</option>
                                                <option value="Return">Return</option>
                                                <option value="Delivered">Delivered</option>
                                            </select>
                                        </div>
                                    </div>


                                    <div className="col-4">
                                        {this.state.shipmentServiceData}
                                        <div className="form-group">
                                            <label htmlFor="">Shipment Name</label>
                                            <select onChange={this.onChangeHandle}
                                                className="form-select" name="shipmentname" value={this.state.shipmentname}>
                                                <option value="">Select</option>
                                                {this.state.shipmentServices.map((data, i) =>
                                                    <option key={i} value={data.name}>{data.name}</option>
                                                )}
                                            </select>
                                        </div>
                                    </div>


                                    <div className="col-6">
                                        <div className="form-group">
                                            <label htmlFor="">Customer Name </label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="customername" value={this.state.customername} />
                                        </div>
                                    </div>

                                    <div className="col-6">
                                        <div className="form-group">
                                            <label htmlFor="">Customer Contact No. </label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="customercontactno" value={this.state.customercontactno} />
                                        </div>
                                    </div>

                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">To Address</label>
                                            <textarea type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="toaddress" value={this.state.toaddress} />
                                        </div>
                                    </div>

                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">From Address </label>
                                            <textarea type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="fromaddress" value={this.state.fromaddress} />
                                        </div>
                                    </div>


                                    {/* <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Status Update Date </label>
                                    <input type="date" onChange={this.onChangeHandle}
                                        className="form-control" name="statusupdatedate" value={this.state.statusupdatedate} />
                                </div>
                            </div> */}


                                    <div className="col-4">
                                        <div className="form-group">
                                            <label htmlFor="">Comments </label>
                                            <textarea type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="comments" value={this.state.comments} />
                                        </div>
                                    </div>

                                    <div className="mt-3">
                                        {this.state.editData ?
                                            <button className="btn btn-primary" onClick={this.onClickUpdate}>Update</button> :
                                            <button className="btn btn-primary" onClick={this.onClickSubmit}>Submit</button>}
                                    </div>
                                </div>
                            </div>
                        </div> :
                        <div>
                            <div>
                                <ViewParcelData data={this.state.allPercelsData} viewParcels={this.viewParcels} getAllParcelsData={this.getAllParcelsData} editParcels={this.editParcels} />
                            </div>
                        </div>}
                </div>
            </div>
        );
    }
}

export default Parcels;