import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewCustomer extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
        this.deleteData = this.deleteData.bind(this)
        this.updateData = this.updateData.bind(this)
    }


    deleteData(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/customerdelete`, dataObject).then(() => {
            console.log(`User ${e.name} removed.`)
            this.props.getAllCustomerData()
        })
            .catch(error => console.error(`There was an error removing the ${e.name} User: ${error}`))
    }


    updateData(e) {
        this.props.updateData(e)
    }


    render() {
        return (
            <div>
                <div className="mt-3">
                    <div className="fieldset">
                        <span className="legend">All Customers</span>
                        {this.props.data.length > 0 ?
                            <table className="table table-striped table-hover">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>Name</th>
                                        <th>Email</th>
                                        <th>Contact</th>
                                        <th>Address</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>

                                    {this.props.data.map((data, i) =>
                                        <tr key={i}>
                                            <td>{data.id}</td>
                                            <td>{data.name}</td>
                                            <td>{data.emailid}</td>
                                            <td>{data.contactno}</td>
                                            <td>{data.address}</td>
                                            <td>
                                                <span className="text-primary me-1 pointer" onClick={() => this.updateData(data)}>Edit</span>
                                                <span className="text-danger ms-1 pointer" onClick={() => this.deleteData(data)}>Delete</span>
                                            </td>
                                        </tr>
                                    )}


                                </tbody>
                            </table> : <div className="text-center fw-bold"> Users not found</div>}
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewCustomer;