import React, { Component } from 'react';
import apiService from '../ApiService/ApiService';

class ViewDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            singleData: this.props.data,
            productid:this.props.productid
        }
        this.Capitalize = this.Capitalize.bind(this)
        this.closeView = this.closeView.bind(this)
    }

    componentDidMount(){
        console.log(this.state.productid);
    }

  


    Capitalize(str) {
        return str.charAt(0).toUpperCase() + str.slice(1);
    }

    closeView(){
        this.props.closeView()
    }
    render() {
        console.log( this.state.productid);
        return (
            <div>
                <div>
                    <div className="row">
                        <div className="col"><div className="fs-5 badge bg-secondary">View Details</div></div>
                        <div className="col text-end">
                            <div className="fs-5 badge bg-danger pointer" onClick={this.closeView}>X</div>
                        </div>
                    </div>
                </div>
                {this.state.singleData.length > 0 ? <div>
                    <div className="row">
                        {Object.keys(this.state.singleData[0]).map((data, i) =>

                            <div className={data === 'images' || data === 'id' ? 'd-none' : 'col-3 py-3'} key={i}>
                                <div className="text-secondary">{this.Capitalize(data.replaceAll('_', ' '))} </div>
                                <div>{this.state.singleData[0][data]}</div>
                            </div>

                        )}
                    </div>
                    <div className="row">
                        {Object.keys(this.state.singleData[0]).map((data, i) =>

                            <div className={data === 'images'?'col12 py-3':'d-none'}  key={i}>
                                <div className="text-secondary">{this.Capitalize(data.replaceAll('_', ' '))} </div>
                                <div>{this.state.singleData[0][data] === null ? <div>images not available</div> :
                                   
                                   <div className="row">
                                       {data === 'images'? JSON.parse(this.state.singleData[0][data]).map((data, i) => 
                                       <div className="col-2" key={i}>
                                          <img className="w-100" src={`${apiService.apiUrl()}/users/images/${this.state.productid}/${data}`} alt="" />
                                       </div>):''}
                                    </div>
                                }</div>
                            </div>

                        )}
                    </div>
                </div> :
                    <div>Data not available</div>}
            </div>
        );
    }
}

export default ViewDetails;