import React, { Component } from 'react';
import { Switch, Route } from 'react-router';
import Purchase from './Purchase/Purchase';
import Sales from './Sales/Sales';
import Vendors from './Vendors/Vendors';
import Products from './Products/Products';
import Staff from './Staff/Staff';
import Category from './Category/Category'
// import Users from './Users/Users'
import DashboardData from './DashboardData';
import ShipmentServices from './ShipmentServices/ShipmentServices';
import apiService from '../ApiService/ApiService';
import axios from 'axios';
import PageNotFound from './PageNotFound';
import Parcels from './Parcels/Parcels';
import ViewParclesDetails from './Parcels/ViewParclesDetails';
import Orders from './Orders/Orders';
import ViewOrdersDetails from './Orders/ViewOrdersDetails';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSignOutAlt } from '@fortawesome/free-solid-svg-icons';
import Customers from './Customers/Customers';
import Salebills from './SaleBills/Salebills';
import ViewPaymentDetails from './SaleBills/ViewPaymentDetails';
import { NavLink } from 'react-router-dom';
import '../StyleSheets/Style.scss'
import PaymentReport from './SaleBills/PaymentReport';
// import axios from 'axios';
// import apiService from '../ApiService/ApiService';
class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            urlEndPoint: "",
            tablesLength: []
        }
        this.onChnageTab = this.onChnageTab.bind(this)
        this.getAllTablesLength = this.getAllTablesLength.bind(this)
        this.onclickCards = this.onclickCards.bind(this)
    }

    componentDidMount() {
        this.getAllTablesLength()
        this.setState({
            urlEndPoint: window.location.pathname
        })
    }

    onChnageTab() {
        // console.log(e.target.id);
        console.log(window.location.pathname);
        // this.setState({
        //     urlEndPoint: window.location.pathname
        // })
    }

    getAllTablesLength() {
        axios.get(`${apiService.apiUrl()}/users/alltableslength`).then(response => {
            // alert(1)
            console.log(response.data);
            this.setState({
                tablesLength: response.data
            })
        })
    }

    onclickCards(data) {
        console.log(data)
        if (data === 'Staff') {
            this.props.history.push(`/dashboard/staff`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Customers') {
            this.props.history.push(`/dashboard/customers`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Vendors') {
            this.props.history.push(`/dashboard/venders`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Shipment Services') {
            this.props.history.push(`/dashboard/shipmentservice`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Sales') {
            this.props.history.push(`/dashboard/sales`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Purchase') {
            this.props.history.push(`/dashboard/purchase`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Categories') {
            this.props.history.push(`/dashboard/categories`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Products') {
            this.props.history.push(`/dashboard/products`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Packages') {
            this.props.history.push(`/dashboard/parcels`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'Orders') {
            this.props.history.push(`/dashboard/orders`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        if (data === 'reports') {
            this.props.history.push(`/dashboard/reports`)
            this.setState({
                urlEndPoint: window.location.pathname
            })
        }
        // if (data === 'Salebills') {
        //     this.props.history.push(`/dashboard/salebills`)
        //     this.setState({
        //         urlEndPoint: window.location.pathname
        //     })
        // }
    }



    render() {
        return (
            <div>
                <div className="container-fluid ">
                    <div className="fixed-top bg-dark text-white">
                        <div className="container">
                            <div className="navbar navbar-expand-lg navbar-dark bg-dark">
                                <div className="container-fluid">
                                    <div className="navbar-brand" >Control Panel</div>
                                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                        <span className="navbar-toggler-icon"></span>
                                    </button>
                                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                            <li className="nav-item">
                                                <NavLink to="/dashboard/" >
                                                    <div className={window.location.pathname === "/dashboard/" ? "nav-link active" : "nav-link"}
                                                    >Home</div>
                                                </NavLink>
                                            </li>
                                            <li className="nav-item">
                                                <NavLink to="/dashboard/staff" >
                                                    <div className={window.location.pathname === "/dashboard/staff" ? "nav-link active" : "nav-link"}
                                                    >Staff</div>
                                                </NavLink>
                                            </li>
                                            <li className="nav-item">
                                                <NavLink to="/dashboard/customers" >
                                                    <div className={window.location.pathname === "/dashboard/customers" ? "nav-link active" : "nav-link"}
                                                    >Customers</div>
                                                </NavLink>
                                            </li>
                                            <li className="nav-item">
                                                <NavLink to="/dashboard/venders" >
                                                    <div className={window.location.pathname === "/dashboard/venders" ? "nav-link active" : "nav-link"}
                                                    >Vendors</div>
                                                </NavLink>
                                            </li>


                                            <li className="nav-item dropdown">
                                                <div className="nav-link dropdown-toggle"
                                                    id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                                    <span className={window.location.pathname === '/dashboard/products' || window.location.pathname === '/dashboard/purchase' || window.location.pathname === '/dashboard/sales' ? 'active p-2' : ''}>Products</span>
                                                </div>
                                                <ul className="dropdown-menu" aria-labelledby="navbarDropdown" >

                                                    <NavLink className="dropdown-item" to="/dashboard/products"  >
                                                        All Products
                                                    </NavLink>

                                                    <NavLink className="dropdown-item" to="/dashboard/purchase" >
                                                        Purchase
                                                    </NavLink>

                                                    <NavLink className="dropdown-item" to="/dashboard/sales" >
                                                        Sales
                                                    </NavLink>
                                                </ul>
                                            </li>

                                            <li className="nav-item dropdown">
                                                <div className="nav-link dropdown-toggle pointer"
                                                    id="droporder" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                                    <span className={window.location.pathname === '/dashboard/orders' || window.location.pathname === '/dashboard/parcels' || window.location.pathname === '/dashboard/shipmentservice' ? 'active p-2' : ''}>Orders</span>
                                                </div>
                                                <ul className="dropdown-menu" aria-labelledby="droporder">

                                                    <NavLink className="dropdown-item" to="/dashboard/orders" >
                                                        All Ordes
                                                    </NavLink>

                                                    <NavLink className="dropdown-item" to="/dashboard/parcels" >
                                                        Packages
                                                    </NavLink>

                                                    <NavLink className="dropdown-item" to="/dashboard/shipmentservice" >
                                                        Shipment Services
                                                    </NavLink>


                                                </ul>
                                            </li>

                                            <li className="nav-item">
                                                <NavLink to="/dashboard/reports" >
                                                    <div className={window.location.pathname === "/dashboard/reports" ? "nav-link active" : "nav-link"}
                                                    >Reports</div>
                                                </NavLink>
                                            </li>
                                        </ul>
                                        <form className="d-flex">
                                            <div className="mt-1 me-3 fs-5" >Admin User</div>
                                            <div className="mt-2"><FontAwesomeIcon className="pointer text-danger" onClick={() => this.props.history.push("/")} icon={faSignOutAlt} /></div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="pages-container" >
                    <div className=" mt-3">
                        <Switch>
                            <Route path="/dashboard/reports" component={PaymentReport} />
                            <Route path="/dashboard/viewpaymentdetails" component={ViewPaymentDetails} />
                            <Route path="/dashboard/salebills" component={Salebills} />
                            <Route path="/dashboard/salebills" component={Salebills} />
                            <Route path="/dashboard/orders/vieworderdetails" component={ViewOrdersDetails} />
                            <Route path="/dashboard/orders" component={Orders} />
                            <Route path="/dashboard/parcels/viewdetails" component={ViewParclesDetails} />
                            <Route path="/dashboard/parcels" component={Parcels} />
                            <Route path="/dashboard/shipmentservice" component={ShipmentServices} />
                            <Route path="/dashboard/sales" component={Sales} />
                            <Route path="/dashboard/purchase" component={Purchase} />
                            <Route path="/dashboard/venders" component={Vendors} />
                            <Route path="/dashboard/products" component={Products} />
                            <Route path="/dashboard/categories" component={Category} />
                            <Route path="/dashboard/staff" component={Staff} />
                            <Route path="/dashboard/customers" component={Customers} />
                            <Route exact path="/dashboard/" >
                                <DashboardData data={this.state.tablesLength} updateData={this.getAllTablesLength} onclickCards={this.onclickCards} />
                            </Route>
                            <Route component={PageNotFound} />
                        </Switch>
                    </div>
                </div>

            </div>
        );
    }
}

export default Dashboard;