import axios from 'axios';
import moment from 'moment';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewSalesData extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
        this.deleteSales = this.deleteSales.bind(this)
        this.editSales = this.editSales.bind(this)
    }


    deleteSales(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/Salesdelete`, dataObject).then(() => {
            console.log(`Sales ${e.idcode} removed.`)
            this.props.getAllSalesData()
        })
            .catch(error => console.error(`There was an error removing the ${e.idcode} Staff: ${error}`))
    }

    editSales(e) {
        console.log(e);
        this.props.editSales(e)
    }


    render() {
        return (
            <div>
                <div className="mt-3">
                    <div className="fieldset">
                        <span className="legend">All Sales Data</span>

                        <div className="mt-2">
                            {this.props.data.length > 0 ?
                                <table className="table table-striped table-hover">
                                    <thead>
                                        <tr>
                                            <th>Bill No</th>
                                            <th>Category</th>
                                            <th>Product</th>
                                            <th>Customer</th>
                                            <th>Date</th>
                                            <th>Quantity</th>
                                            <th>Price</th>
                                            <th>Total</th>
                                            <th>Status</th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.props.data.map((data, i) =>
                                            <tr key={i}>
                                                <td>{data.idcode}</td>
                                                <td>{data.category}</td>
                                                <td>{data.product}</td>
                                                <td>{data.customer}</td>
                                                <td>{moment(data.date).format('DD-MM-YYYY')}</td>
                                                <td>{data.qty}</td>
                                                <td>{data.price}</td>
                                                <td>{data.total}</td>
                                                <td>{data.status}</td>
                                                <td>
                                                    <span className="text-primary me-1 pointer" onClick={() => this.editSales(data)}>Edit</span>
                                                    <span className="text-danger ms-1 pointer" onClick={() => this.deleteSales(data)}>Delete</span>
                                                </td>
                                            </tr>)}

                                    </tbody>
                                </table> : <div className="text-center fw-bold"> Sales not found</div>}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewSalesData;