import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewStaffData extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }

        this.deleteStaff = this.deleteStaff.bind(this)
        this.updateStaff = this.updateStaff.bind(this)
    }



    updateStaff(e) {
        this.props.updateStaff(e)
    }


    deleteStaff(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/staffdelete`, dataObject).then(() => {
            console.log(`Staff ${e.name} removed.`)
            this.props.getAllStaff()
        })
            .catch(error => console.error(`There was an error removing the ${e.name} Staff: ${error}`))
    }


    render() {
        return (
            <div>
                <div className="fieldset">
                    <span className="legend">All Staff</span>
               
                {this.props.data.length > 0 ?
                    <table className="table table-striped table-hover">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>Name</th>
                                <th>Email</th>
                                {/* <th>Password</th> */}
                                <th>Contact</th>
                                <th>Address</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>

                            {this.props.data.map((data, i) =>
                                <tr key={i}>
                                    <td>{data.id}</td>
                                    <td>{data.name}</td>
                                    <td>{data.email}</td>
                                    {/* <td>{data.password}</td> */}
                                    <td>{data.contactno}</td>
                                    <td>{data.address}</td>
                                    <td>
                                        <span className="text-primary me-1 pointer" onClick={() => this.updateStaff(data)}>Edit</span>
                                        <span className="text-danger ms-1 pointer" onClick={() => this.deleteStaff(data)}>Delete</span>
                                    </td>
                                </tr>
                            )}


                        </tbody>
                    </table> : <div className="text-center fw-bold"> Staff not found</div>}
                    </div>
            </div>
        );
    }
}

export default ViewStaffData;