import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewShipmentData from './ViewShipmentData';

class ShipmentServices extends Component {
    constructor(props) {
        super(props);
        this.state = {
            getAllShipmentserviceArr: [],
            name: '',
            email: '',
            contactno: '',
            address: '',
            singleshipmentserviceData: '',
            editShipmentservice: false,
            openform: false
        }
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.getAllShipmentservice = this.getAllShipmentservice.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.editShipmentservice = this.editShipmentservice.bind(this)
        this.onClickUpdate = this.onClickUpdate.bind(this)
        this.onClickAdd = this.onClickAdd.bind(this)
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onClickAdd() {
        this.setState({
            openform: !this.state.openform,
            name: '',
            email: '',
            contactno: '',
            address: '',
        })
    }

    componentDidMount() {
        this.getAllShipmentservice()
    }

    getAllShipmentservice() {
        axios.get(`${apiService.apiUrl()}/users/shipmentserviceall`).then(response => {
            console.log(response.data);
            this.setState({
                getAllShipmentserviceArr: response.data
            })
        }).catch(error => console.error(`something went to wrong ${error}`))
    }



    onClickSubmit() {
        const dataObject = {
            name: this.state.name,
            email: this.state.email,
            contactno: this.state.contactno,
            address: this.state.address
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/createshipmentservice`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                openform: false,
                name: '',
                email: '',
                contactno: '',
                address: '',
                singleshipmentserviceData: ''
            })
            this.getAllShipmentservice()
        }).catch(error => console.error(`something went to wrong ${error}`))
    }

    editShipmentservice(data) {
        this.setState({
            singleshipmentserviceData: data,
            editShipmentservice: true,
            openform: true,
            name: data.name,
            email: data.email,
            contactno: data.contactno,
            address: data.address
        })
    }

    onClickUpdate() {
        const dataObject = {
            idcode: this.state.singleshipmentserviceData.idcode,
            name: this.state.name,
            email: this.state.email,
            contactno: this.state.contactno,
            address: this.state.address
        }
        axios.post(`${apiService.apiUrl()}/users/updateshipmentservice`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                openform: false,
                name: '',
                email: '',
                contactno: '',
                address: '',
                singleshipmentserviceData: ''
            })
            this.getAllShipmentservice()
        }).catch(error => console.error(`something went to wrong ${error}`))
    }


    render() {
        return (
            <div>
                <div className="container">

                    <div className="text-end mt-3">
                        {this.state.openform ?
                            <button className="btn btn-danger" onClick={this.onClickAdd}>Close</button> :
                            <button className="btn btn-primary" onClick={this.onClickAdd}>Add</button>}
                    </div>
                    <div className="row">
                    <div className={this.state.openform?"col-10":"col-12"}>
                        <div>
                            <ViewShipmentData data={this.state.getAllShipmentserviceArr} getAllshipmentservice={this.getAllShipmentservice} editShipmentservice={this.editShipmentservice} />
                        </div>
                    </div>
                        {this.state.openform ?
                            <div className="col-2">
                                <div className="mt-3">
                                <div className="fieldset">
                                    <span className="legend">Add New</span>
                                <div className="col-12">
                                    <div className="form-group">
                                        <label htmlFor="">Name</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="name" value={this.state.name} />
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="form-group">
                                        <label htmlFor="">Email ID</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="email" value={this.state.email} />
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="form-group">
                                        <label htmlFor="">Contact No</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="contactno" value={this.state.contactno} />
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="form-group">
                                        <label htmlFor="">Address</label>
                                        <textarea type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="address" value={this.state.address} />
                                    </div>
                                </div>

                                <div className="mt-2">
                                    {this.state.editShipmentservice ?
                                        <button className="btn btn-primary w-100" onClick={this.onClickUpdate}>Update</button> :
                                        <button className="btn btn-primary w-100" onClick={this.onClickSubmit}>Submit</button>}
                                </div>
                                </div>
                                </div>
                            </div> : ""}
                    </div>

                </div>
            </div>
        );
    }
}

export default ShipmentServices;