import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewCategoryData extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }

        this.updateCategory = this.updateCategory.bind(this)
        this.deleteCategory = this.deleteCategory.bind(this)
    }


    updateCategory(e) {
        this.props.editcategory(e)
    }

    deleteCategory(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/categorydelete`, dataObject).then(() => {
            console.log(`Category ${e.name} removed.`)
            this.props.getAllCategory()
        })
            .catch(error => console.error(`There was an error removing the ${e.name} Category: ${error}`))
    }

    render() {
        return (
            <div>
                <div className="mt-3">
                    <div className="fieldset">
                        <span className="legend">All Categories</span>

                        {this.props.data.length > 0 ?
                            <table className="table table-striped table-hover">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>Category Name</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>

                                    {this.props.data.map((data, i) =>
                                        <tr key={i}>
                                            <td>{data.id}</td>
                                            <td>{data.categoryname}</td>
                                            <td>
                                                <span className="text-primary me-1 pointer" onClick={() => this.updateCategory(data)}>Edit</span>
                                                <span className="text-danger ms-1 pointer" onClick={() => this.deleteCategory(data)}>Delete</span>
                                            </td>
                                        </tr>
                                    )}


                                </tbody>
                            </table> : <div className="text-center fw-bold"> Category not found</div>}
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewCategoryData;