
import React, { Component } from 'react';

class DashboardData extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tablesLength: []
        }
        this.onclickCards = this.onclickCards.bind(this)
    }
    componentDidMount() {
        this.props.updateData()
    }


    onclickCards(e) {
        console.log(e.title);
        this.props.onclickCards(e.title)
    }


    render() {
        return (
            <div>
                <div className="container">
                    <div className="mt-5">
                        <div className="fieldset">
                            <span className="legend">Dashboard</span>
                            <div className="row m-0">
                                {this.props.data.map((data, i) =>
                                    <div key={i} className="col-3 p-3" onClick={() => this.onclickCards(data)}>
                                        <div className="card">
                                            <div className="card-body">
                                                <h4 className="card-title">{data.title}</h4>
                                                <p className="card-text">{data.length}</p>
                                            </div>
                                        </div>
                                    </div>)}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default DashboardData;