import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewSalesData from './ViewSalesData';

class Sales extends Component {
    constructor(props) {
        super(props);
        this.state = {
            category: '',
            product: '',
            customer: '',
            date: '',
            qty: '',
            price: '',
            total: '',
            status: '',
            searchCategoryArr: [],
            searchCategory: false,
            searchProductArr: [],
            searchProduct: false,
            searchcustomerArr: [],
            searchcustomer: false,
            allSalesData: [],
            editSales: false,
            openForm: false,
        }
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.onClickCategoryValue = this.onClickCategoryValue.bind(this)
        this.onClickProductValue = this.onClickProductValue.bind(this)
        this.onClickcustomerValue = this.onClickcustomerValue.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.getAllSalesData = this.getAllSalesData.bind(this)
        this.editSales = this.editSales.bind(this)
        this.updateSales = this.updateSales.bind(this)
        this.onClickAdd = this.onClickAdd.bind(this)
    }

    componentDidMount() {
        this.getAllSalesData()
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        }, () => {

            if (e.target.name === "category") {
                if (this.state.category === "") {
                    this.setState({
                        searchCategory: false
                    })
                } else {
                    this.setState({
                        searchCategory: true
                    })
                    const dataObject = {
                        categoryname: this.state.category
                    }
                    axios.post(`${apiService.apiUrl()}/users/categoryexists`, dataObject).then(response => {
                        console.log(response.data);
                        this.setState({
                            searchCategoryArr: response.data
                        })
                    }).catch(error => console.error(`something went to wrong ${error}`))
                }


            }

            if (e.target.name === "product") {
                if (this.state.product === "") {
                    this.setState({
                        searchProduct: false
                    })
                } else {
                    this.setState({
                        searchProduct: true
                    })
                    const dataObject = {
                        productname: this.state.product
                    }
                    axios.post(`${apiService.apiUrl()}/users/productsexists`, dataObject).then(response => {
                        console.log(response.data);
                        this.setState({
                            searchProductArr: response.data
                        })

                    }).catch(error => console.error(`something went to wrong ${error}`))
                }
            }

            if (e.target.name === "customer") {
                if (this.state.customer === "") {
                    this.setState({
                        searchcustomer: false
                    })
                } else {
                    this.setState({
                        searchcustomer: true
                    })
                    const dataObject = {
                        name: this.state.customer
                    }
                    axios.post(`${apiService.apiUrl()}/users/usersexists`, dataObject).then(response => {
                        console.log(response.data);
                        this.setState({
                            searchcustomerArr: response.data
                        })
                    }).catch(error => console.error(`something went to wrong ${error}`))
                }
            }

            if (e.target.name === "price" || e.target.name === "qty") {
                if (this.state.price === "") {
                    this.setState({
                        total: 0
                    })
                } else {
                    this.setState({
                        total: this.state.price * this.state.qty
                    })
                }
            }
        })
    }

    onClickCategoryValue(e) {
        this.setState({
            category: e.target.id,
            searchCategory: false
        })
    }

    onClickProductValue(e) {
        this.setState({
            product: e.target.id,
            searchProduct: false
        })
    }
    onClickcustomerValue(e) {
        this.setState({
            customer: e.target.id,
            searchcustomer: false
        })
    }

    getAllSalesData() {
        axios.get(`${apiService.apiUrl()}/users/salesall`).then(response => {
            console.log(response.data);
            this.setState({
                allSalesData: response.data
            })
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    onClickSubmit() {
        const dataObject = {
            category: this.state.category,
            product: this.state.product,
            customer: this.state.customer,
            date: this.state.date,
            qty: this.state.qty,
            price: this.state.price,
            total: this.state.price * this.state.qty,
            status: this.state.status
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/createsales`, dataObject).then(response => {
            console.log(response.data);
            this.getAllSalesData()
            this.setState({
                openForm: false,
                editSales: false,
                category: '',
                product: '',
                customer: '',
                date: '',
                qty: '',
                price: '',
                total: '',
                status: '',
            })
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    editSales(data) {
        this.setState({
            singleSalesData: data,
            openForm: true,
            editSales: true,
            category: data.category,
            product: data.product,
            customer: data.customer,
            date: data.date,
            qty: data.qty,
            price: data.price,
            total: data.price * data.qty,
            status: data.status
        })

    }

    updateSales() {
        const dataObject = {
            idcode: this.state.singleSalesData.idcode,
            category: this.state.category,
            product: this.state.product,
            customer: this.state.customer,
            date: this.state.date,
            qty: this.state.qty,
            price: this.state.price,
            total: this.state.price * this.state.qty,
            status: this.state.status
        }
        axios.post(`${apiService.apiUrl()}/users/updatesales`, dataObject).then(response => {
            console.log(response.data);
            this.getAllSalesData()
            this.setState({
                openForm: false,
                editSales: false,
                category: '',
                product: '',
                customer: '',
                date: '',
                qty: '',
                price: '',
                total: '',
                status: '',
            })
        }).catch(error => console.error(`Something went to wrong ${error}`))
    }

    onClickAdd() {
        this.setState({
            openForm: !this.state.openForm,
            editSales: false,
            category: '',
            product: '',
            customer: '',
            date: '',
            qty: '',
            price: '',
            total: '',
            status: '',
        })
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="mt-3 text-end">
                        {this.state.openForm ?
                            <button className="btn btn-danger" onClick={this.onClickAdd}>Close</button> :
                            <button className="btn btn-primary" onClick={this.onClickAdd}>Add</button>}
                    </div>
                    <div className="row mt-3">
                        <div className={this.state.openForm ? "col-10" : "col-12"}>
                            <div className="">
                                <ViewSalesData data={this.state.allSalesData} getAllSalesData={this.getAllSalesData} editSales={this.editSales} />
                            </div>
                        </div>
                        {this.state.openForm ?
                            <div className="col-2 mt-3">
                                <div className="fieldset">
                                    <span className="legend">Add New</span>


                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Category</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="category" value={this.state.category} />
                                            {this.state.searchCategory ?
                                                <div className="col-3 bg-white shadow" style={{ maxHeight: 100, position: "absolute", overflow: "auto" }}>
                                                    {this.state.searchCategoryArr.map((data, i) =>
                                                        <div className="px-2" key={i}>
                                                            <p className="mt-3" id={data.categoryname} onClick={this.onClickCategoryValue}>{data.categoryname}</p>
                                                        </div>
                                                    )}
                                                </div> : ""}
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Product</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="product" value={this.state.product} />
                                            {this.state.searchProduct ?
                                                <div className="col-3 bg-white shadow" style={{ maxHeight: 100, position: "absolute", overflow: "auto" }}>
                                                    {this.state.searchProductArr.map((data, i) =>
                                                        <div className="px-2" key={i}>
                                                            <p className="mt-3" id={data.productname} onClick={this.onClickProductValue}>{data.productname}</p>
                                                        </div>
                                                    )}
                                                </div> : ""}
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Customer</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="customer" value={this.state.customer} />
                                            {this.state.searchcustomer ?
                                                <div className="col-3 bg-white shadow" style={{ maxHeight: 100, position: "absolute", overflow: "auto" }}>
                                                    {this.state.searchcustomerArr.map((data, i) =>
                                                        <div className="px-2" key={i}>
                                                            <p className="mt-3" id={data.name} onClick={this.onClickcustomerValue}>{data.name}</p>
                                                        </div>
                                                    )}
                                                </div> : ""}
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Date</label>
                                            <input type="date" onChange={this.onChangeHandle}
                                                className="form-control" name="date" value={this.state.date} />
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Quantity</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="qty" value={this.state.qty} />
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Price</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="price" value={this.state.price} />
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Total</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="total" value={this.state.total} readOnly />
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className="form-group">
                                            <label htmlFor="">Status</label>
                                            <select onChange={this.onChangeHandle}
                                                className="form-select" name="status" value={this.state.status} >
                                                <option value="">Select</option>
                                                <option value="Pending">Pending</option>
                                                <option value="Completed">Completed</option>
                                            </select>
                                        </div>
                                    </div>

                                    <div className="my-3">
                                        {this.state.editSales ?
                                            <button className="btn btn-primary w-100" onClick={this.updateSales}>Update</button> :
                                            <button className="btn btn-primary w-100" onClick={this.onClickSubmit}>Submit</button>}
                                    </div>
                                </div>
                            </div> : ""}
                    </div>


                </div>
            </div>
        );
    }
}

export default Sales;