import moment from 'moment';
import React, { Component } from 'react';

class ViewParclesDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            singleParcleData: JSON.parse(localStorage.getItem('singleParcelData'))
        }
    }



    render() {
        console.log(this.state.singleParcleData);
        return (
            <div>
                <div className="container">

                    <div className="col-12 text-end">
                        <button className="btn btn-danger" onClick={() => this.props.history.push('/dashboard/parcels')}>Close</button>
                    </div>
                    <div className="mt-3">
                        <div className="fieldset">
                            <span className="legend">Parcel Details</span>

                            <div className="mt-3">
                                <div className="row m-0">

                                    <div className="col-2 ">
                                        <small className="text-secondary">Package No.</small>
                                        <p>{this.state.singleParcleData.idcode}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Customer Name.</small>
                                        <p>{this.state.singleParcleData.customername}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Customer Contact No.</small>
                                        <p>{this.state.singleParcleData.customercontactno}</p>
                                    </div>

                                    <div className="col-3">
                                        <small className="text-secondary">Status Update Date / Time</small>
                                        <p><span> {moment(this.state.singleParcleData.statusupdatedate).format('DD-MM-YYYY')} </span> <span>{moment(this.state.singleParcleData.statusupdatedate).format('LTS')}</span></p>
                                    </div>

                                    <hr />

                                    <div className="">
                                        <div className="fs-6 fw-bold">Package Details</div>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Order No.</small>
                                        <p>{this.state.singleParcleData.orderno}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Packing Date</small>
                                        <p>{this.state.singleParcleData.packingdate}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Packing Status</small>
                                        <p>{this.state.singleParcleData.packingstatus}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Package Size</small>
                                        <p>{this.state.singleParcleData.packagesize}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Package Weight</small>
                                        <p>{this.state.singleParcleData.packageweight}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Package Content</small>
                                        <p>{this.state.singleParcleData.packagecontent}</p>
                                    </div>

                                    <hr />

                                    <div className="">
                                        <div className="fs-6 fw-bold">Shipment Service Details</div>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Shipment Name</small>
                                        <p>{this.state.singleParcleData.shipmentname}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Shipment Code</small>
                                        <p>{this.state.singleParcleData.shipmentcode}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">Shipment Status</small>
                                        <p>{this.state.singleParcleData.shipmentstatus}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">To Address</small>
                                        <p>{this.state.singleParcleData.toaddress}</p>
                                    </div>

                                    <div className="col-2">
                                        <small className="text-secondary">From Address</small>
                                        <p>{this.state.singleParcleData.fromaddress}</p>
                                    </div>

                                    <hr />

                                    <div className="">
                                        <small className="text-secondary">Comments</small>
                                        <p>{this.state.singleParcleData.comments}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewParclesDetails;