import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewVendorsData from './ViewVendorsData';

class Vendors extends Component {
    constructor(props) {
        super(props);
        this.state = {
            getAllVenderArr: [],
            name: '',
            email: '',
            contactno: '',
            address: '',
            singleVenderData: '',
            editVender: false,
            openform: false
        }
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.getAllvendors = this.getAllvendors.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.editVendors = this.editVendors.bind(this)
        this.onClickUpdate = this.onClickUpdate.bind(this)
        this.onClickAdd = this.onClickAdd.bind(this)
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onClickAdd() {
        this.setState({
            openform: !this.state.openform,
            name: '',
            email: '',
            contactno: '',
            address: '',
            singleVenderData: '',
        })
    }

    componentDidMount() {
        this.getAllvendors()
    }

    getAllvendors() {
        axios.get(`${apiService.apiUrl()}/users/vendorsall`).then(response => {
            console.log(response.data);
            this.setState({
                getAllVenderArr: response.data
            })
        }).catch(error => console.error(`something went to wrong ${error}`))
    }



    onClickSubmit() {
        const dataObject = {
            name: this.state.name,
            email: this.state.email,
            contactno: this.state.contactno,
            address: this.state.address
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/createvendors`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                openform: false,
                name: '',
                email: '',
                contactno: '',
                address: '',
                singleVenderData: ''
            })
            this.getAllvendors()
        }).catch(error => console.error(`something went to wrong ${error}`))
    }

    editVendors(data) {
        this.setState({
            singleVenderData: data,
            editVender: true,
            openform: true,
            name: data.name,
            email: data.email,
            contactno: data.contactno,
            address: data.address
        })
    }

    onClickUpdate() {
        const dataObject = {
            idcode: this.state.singleVenderData.idcode,
            name: this.state.name,
            email: this.state.email,
            contactno: this.state.contactno,
            address: this.state.address
        }
        axios.post(`${apiService.apiUrl()}/users/updatevendors`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                openform: false,
                name: '',
                email: '',
                contactno: '',
                address: '',
                singleVenderData: ''
            })
            this.getAllvendors()
        }).catch(error => console.error(`something went to wrong ${error}`))
    }


    render() {
        return (
            <div>
                <div className="container">

                    <div className="text-end mt-3">
                        {this.state.openform ?
                            <button className="btn btn-danger" onClick={this.onClickAdd}>Close</button> :
                            <button className="btn btn-primary" onClick={this.onClickAdd}>Add</button>}
                    </div>
                    <div className="row">
                        <div className={this.state.openform ? "col-10" : "col-12"}>
                            <div>
                                <ViewVendorsData data={this.state.getAllVenderArr} getAllvendors={this.getAllvendors} editVendors={this.editVendors} />
                            </div>
                        </div>
                        {this.state.openform ?
                            <div className="col-2 mt-3">
                                <div className="fieldset">
                                    <span className="legend">Add New</span>


                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Name</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="name" value={this.state.name} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Email ID</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="email" value={this.state.email} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Contact No</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="contactno" value={this.state.contactno} />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label htmlFor="">Address</label>
                                            <textarea type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="address" value={this.state.address} />
                                        </div>
                                    </div>

                                    <div className="mt-2">
                                        {this.state.editVender ?
                                            <button className="btn btn-primary w-100" onClick={this.onClickUpdate}>Update</button> :
                                            <button className="btn btn-primary w-100" onClick={this.onClickSubmit}>Submit</button>}
                                    </div>
                                </div>
                            </div> : ""}
                    </div>


                </div>
            </div>
        );
    }
}

export default Vendors;