import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../ApiService/ApiService';

class Filters extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fromdate: '',
            todate: '',
            searchtype: '',
            searchvalue: '',
            fieldtype: ''
        }
        this.omChangeHandle = this.omChangeHandle.bind(this)
        this.searchBtn = this.searchBtn.bind(this)
    }

    omChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    searchBtn() {

        if (this.state.searchtype === 'date') {
            const dataObject = {
                tablename: this.props.data.tablename,
                fieldname: this.props.data.fieldname,
                fromdate: this.state.fromdate,
                todate: this.state.todate
            }
            // console.log(dataObject);
            axios.post(`${apiService.apiUrl()}/users/datefilter`, dataObject).then(res => {
                console.log(res.data);
                this.props.searchBtn(res.data)
            })
        } else {
            const dataObject = {
                tablename: this.props.data.tablename,
                fieldname: this.state.fieldtype,
                searchvalue: this.state.searchvalue,
            }
            // console.log(dataObject);
            axios.post(`${apiService.apiUrl()}/users/valuefilter`, dataObject).then(res => {
                console.log(res.data);
                this.props.searchBtn(res.data)
            })
        }
    }

    render() {
        return (
            <div>
                <div>
                    <div>

                        <div className="row">
                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Search By</label>
                                    <select type="date" onChange={this.omChangeHandle}
                                        className="form-select" name="searchtype" value={this.state.searchtype} >
                                        <option value="">Select</option>
                                        <option value="date">Date</option>
                                        <option value="value">Data</option>
                                    </select>
                                </div>
                            </div>
                            {this.state.searchtype === 'value' || this.state.searchtype === 'date'?
                            <div className="col-6">
                                {this.state.searchtype === 'date' ?
                                    <div className="row">
                                        <div className="col-6">
                                            <div className="form-group">
                                                <label htmlFor="">From</label>
                                                <input type="date" onChange={this.omChangeHandle}
                                                    className="form-control" name="fromdate" value={this.state.fromdate} />
                                            </div>
                                        </div>
                                        <div className="col-6">
                                            <div className="form-group">
                                                <label htmlFor="">To</label>
                                                <input type="date" onChange={this.omChangeHandle}
                                                    className="form-control" name="todate" value={this.state.todate} />
                                            </div>
                                        </div>
                                    </div>
                                    :  this.state.searchtype === 'value'?
                                    <div className="row">
                                        <div className="col-6">
                                            <div className="form-group">
                                                <label htmlFor="">Search field</label>
                                                <select type="text" onChange={this.omChangeHandle}
                                                    className="form-select" name="fieldtype" value={this.state.searfieldtypechvalue} >
                                                    <option value="">Select</option>
                                                    {this.props.fielddata.map((data, i) =>
                                                    
                                                        <option key={i} value={data.value}>{data.title}</option>
                                                    )}
                                                </select>
                                            </div>
                                        </div>
                                        <div className="col-6">
                                            <div className="form-group">
                                                <label htmlFor="">Search</label>
                                                <input type="text" onChange={this.omChangeHandle}
                                                    className="form-control" name="searchvalue" value={this.state.searchvalue} placeholder="Search Keyword" />
                                            </div>
                                        </div>

                                    </div>:''}
                            </div>:''}



                            <div className="col-3 mt-4">
                                <button className="btn btn-primary" onClick={this.searchBtn}>Search</button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Filters;