import axios from 'axios';
import moment from 'moment';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewOrdersDetails extends Component {
    totalAmount = 0
    constructor(props) {
        super(props);
        this.state = {
            singleOrderData: JSON.parse(localStorage.getItem('singleOrderData')),
            itemsDataArr: [],
            installmentArr: [],
            totalorderAmount: '',
            makepaymentBtn: false
        }

        this.getDataByOrderid = this.getDataByOrderid.bind(this)
        this.itemlist = this.itemlist.bind(this)
        this.onClickMakePayment = this.onClickMakePayment.bind(this)
        this.getInstallmentData = this.getInstallmentData.bind(this)
        this.getSalesBills = this.getSalesBills.bind(this)
        this.onClickViewPaymentDetails = this.onClickViewPaymentDetails.bind(this)
    }

    componentDidMount() {
        this.getDataByOrderid()
        this.getInstallmentData()
        this.getSalesBills()
    }

    getDataByOrderid() {
        const dataObject = {
            orderid: this.state.singleOrderData.idcode
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/getproductsbyorderid`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                itemsDataArr: response.data
            })
        })

    }

    getInstallmentData() {
        const dataObject = {
            orderid: this.state.singleOrderData.idcode
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/getinstallmentbyorderid`, dataObject).then(response => {
            console.log(response.data)
            this.setState({
                installmentArr: response.data
            })
        })
    }

    getSalesBills() {
        axios.get(`${apiService.apiUrl()}/users/salebillsall`).then(response => {
            console.log(response.data)
            var dataArr = response.data
            if (dataArr.length > 0) {
                var index = dataArr.map(e => e.orderid).indexOf(this.state.singleOrderData.idcode)
                console.log(index);
                if (index > -1) {
                    this.setState({
                        makepaymentBtn: true
                    })
                }
            } else {
                this.setState({
                    makepaymentBtn: false
                })
            }

        })
    }

    itemlist() {
        var list = []
        var totalAmount = 0

        for (let i = 0; i < this.state.itemsDataArr.length; i++) {
            var data = this.state.itemsDataArr[i]
            totalAmount += (data.price * data.qty)
            list.push(
                <div key={i}>
                    <div className="row m-0 p-2">
                        <div className="col">{data.category}</div>
                        <div className="col">{data.product}</div>
                        <div className="col">{data.qty}</div>
                        <div className="col">{data.price}</div>
                        <div className="col">{data.total}</div>
                    </div>
                </div>
            )

        }

        list.push(
            <div key={totalAmount} className="p-3">
                <hr />
                <div className="row m-0 p-2">
                    <div className="col"></div>
                    <div className="col"></div>
                    <div className="col"></div>
                    <div className="col"></div>
                    <div className="col fw-bold">Total : {totalAmount}</div>
                </div>
                {this.state.singleOrderData.status === 'Cancel'?<div className="mt-3 text-danger">Order is Canceled</div>:
                <div className="mt-3">
                    {this.state.makepaymentBtn ?
                        <button className="btn btn-primary" onClick={this.onClickViewPaymentDetails}>View Payment Details</button> :
                        <button className="btn btn-primary" onClick={this.onClickMakePayment}>Make Payment</button>}
                </div>}
            </div>
        )
        this.totalAmount = totalAmount
        return <div>{list}</div>
    }

    onClickMakePayment() {
        console.log(this.totalAmount);
        const dataobject = {
            orderid: this.state.singleOrderData.idcode,
            customerid: this.state.singleOrderData.clientid,
            totalamount: this.totalAmount
        }
        console.log(dataobject);
        localStorage.setItem('paymentdata', JSON.stringify(dataobject))
        this.props.history.push(`/dashboard/salebills`)
    }

    onClickViewPaymentDetails() {
        const clientData = {
            orderid: this.state.singleOrderData.idcode,
            clienttype: this.state.singleOrderData.clienttype
        }
        localStorage.setItem('clientData', JSON.stringify(clientData))
        this.props.history.push('/dashboard/viewpaymentdetails')
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="col-12 text-end">
                        <button className="btn btn-danger" onClick={() => this.props.history.push('/dashboard/orders')}>Close</button>
                    </div>

                    <div className="mt-3">
                        <div className="fieldset">
                            <span className="legend">Order Details</span>



                            <div>
                                <div className="row m-0">
                                    <div className="col-2 ">
                                        <small className="text-secondary">Order No.</small>
                                        <p>{this.state.singleOrderData.idcode}</p>
                                    </div>
                                    <div className="col-2 ">
                                        <small className="text-secondary">Order Date</small>
                                        <p>{moment(this.state.singleOrderData.orderdate).format('DD-MM-YYYY')}</p>
                                    </div>
                                    <div className="col-2 ">
                                        <small className="text-secondary">Client ID</small>
                                        <p>{this.state.singleOrderData.clientid}</p>
                                    </div>
                                    <div className="col-2 ">
                                        <small className="text-secondary">Client Type</small>
                                        <p>{this.state.singleOrderData.clienttype}</p>
                                    </div>
                                    <div className="col-2 ">
                                        <small className="text-secondary">Status</small>
                                        <p>{this.state.singleOrderData.status}</p>
                                    </div>
                                </div>
                                <hr />

                                <div className="mt-3">
                                    <div className="fw-bold fs-5">Item List</div>
                                    {this.state.itemsDataArr.length > 0 ?
                                        <div className="border">
                                            <div className="fw-bold my-3 border-bottom">
                                                <div className="row m-0 pb-1">
                                                    <div className="col">Category Name</div>
                                                    <div className="col">Product Name</div>
                                                    <div className="col">Quantity</div>
                                                    <div className="col">Price Per/pcs</div>
                                                    <div className="col">Total</div>
                                                </div>
                                            </div>

                                            {this.itemlist()}
                                            {/* {this.state.itemsDataArr.map((data, i) =>
                                        <div key={i}>
                                            <div className="row m-0 p-2">
                                                <div className="col">{data.category}</div>
                                                <div className="col">{data.product}</div>
                                                <div className="col">{data.qty}</div>
                                                <div className="col">{data.price}</div>
                                                <div className="col">{data.total}</div>
                                            </div>
                                        </div>
                                    )} */}
                                        </div> : <div>Items Not Found</div>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewOrdersDetails;