import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../ApiService/ApiService';

class Product extends Component {
    constructor(props) {
        super(props);
        this.state = {
            
            product: '',
            searchProductArr: [],
            searchProduct: false
        }
        this.onChange = this.onChange.bind(this)
        this.onClickProductValue = this.onClickProductValue.bind(this)
    }

    onChange(e) {

        this.setState({
            [e.target.name]: e.target.value
        }, () => {
           
            if (e.target.name === "product") {
                if (this.state.product === "") {
                    this.setState({
                        searchProduct: false
                    })
                } else {
                    this.setState({
                        searchProduct: true
                    })
                    const dataObject = {
                        productname: this.state.product
                    }
                    axios.post(`${apiService.apiUrl()}/users/productsexists`, dataObject).then(response => {
                        console.log(response.data);
                        this.setState({
                            searchProductArr: response.data
                        })
                       
                    }).catch(error => console.error(`something went to wrong ${error}`))
                }
            }
        })
    }


    onClickProductValue(e) {
        this.setState({
            product: e.target.id,
            searchProduct: false
        })

        this.props.productValue(e.target.id)
    }
    render() {
        return (
            <div>

                <div className="">
                    
                    <div className="">
                        <div className="form-group">
                            <label htmlFor="">Product</label>
                            <input type="text" onChange={this.onChange}
                                className="form-control" name="product" value={this.state.product} />
                            {this.state.searchProduct ?
                                <div className="col-3 bg-white shadow" style={{ maxHeight: 100, position: "absolute", overflow: "auto" }}>
                                    {this.state.searchProductArr.map((data, i) =>
                                        <div className="px-2" key={i}>
                                            <p className="mt-3" id={data.productname} onClick={this.onClickProductValue}>{data.productname}</p>
                                        </div>
                                    )}
                                </div> : ""}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Product;