import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewProductsData from './ViewProductsData';



class Products extends Component {
    constructor(props) {
        super(props);
        this.state = {
            allCategoryArr: [],
            productname: '',
            category: '',
            qty: '',
            details: '',
            allProductsArr: '',
            editproduct: false,
            singleProductData: '',
            openform: false
        }

        this.getAllCategory = this.getAllCategory.bind(this)
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.onClickSubmit = this.onClickSubmit.bind(this)
        this.getAllProducts = this.getAllProducts.bind(this)
        this.editProducts = this.editProducts.bind(this)
        this.onClickUpdate = this.onClickUpdate.bind(this)
        this.addnewProduct = this.addnewProduct.bind(this)
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    componentDidMount() {
        this.getAllCategory()
        this.getAllProducts()
    }

    getAllCategory() {
        axios.get(`${apiService.apiUrl()}/users/categoryall`).then(response => {
            console.log(response.data);
            this.setState({
                allCategoryArr: response.data
            })
        })
    }

    getAllProducts() {
        axios.get(`${apiService.apiUrl()}/users/productsall`).then(response => {
            console.log(response.data);
            this.setState({
                allProductsArr: response.data
            })
        })
    }

    onClickSubmit() {
        const dataObject = {
            productname: this.state.productname,
            category: this.state.category,
            qty: this.state.qty,
            details: this.state.details
        }
        console.log(dataObject);

        axios.post(`${apiService.apiUrl()}/users/createproducts`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                productname: '',
                category: '',
                qty: '',
                details: '',
                allProductsArr: ''
            })
            this.getAllProducts()
        }).catch(error => console.error(`something went to wrong in create product : ${error}`))
    }

    editProducts(data) {
        this.setState({
            openform:true,
            singleProductData: data,
            editproduct: true,
            productname: data.productname,
            category: data.category,
            qty: data.qty,
            details: data.details
        })
    }

    onClickUpdate() {
        const dataObject = {
            idcode: this.state.singleProductData.idcode,
            productname: this.state.productname,
            category: this.state.category,
            qty: this.state.qty,
            details: this.state.details
        }
        console.log(dataObject);

        axios.post(`${apiService.apiUrl()}/users/updateproducts`, dataObject).then(response => {
            console.log(response.data);
            this.setState({
                productname: '',
                category: '',
                qty: '',
                details: '',
                allProductsArr: '',
                openform:false,
            })
            this.getAllProducts()
        }).catch(error => console.error(`something went to wrong in create product : ${error}`))
    }

    addnewProduct() {
        this.setState({
            openform: !this.state.openform,
            productname: '',
            category: '',
            qty: '',
            details: '',
        })
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="text-end mt-3">
                        <button className="btn btn-primary me-2" onClick={() => this.props.history.push("/dashboard/categories")}>Manage Category</button>
                        {this.state.openform ?
                            <button className="btn btn-danger" onClick={this.addnewProduct}>Close</button> : <button className="btn btn-primary" onClick={this.addnewProduct}>Add</button>}
                    </div>

                    <div className="row">

                        <div className={this.state.openform ? "col-10" : "col-12"}>
                            <div>
                                <ViewProductsData data={this.state.allProductsArr} getAllProducts={this.getAllProducts} editProducts={this.editProducts} />
                            </div>
                        </div>
                        {this.state.openform ?
                            <div className="col-2 mt-3">
                                <div className="fieldset">
                                    <span className="legend">Add New</span>

                                    <div className="mt-2">
                                        <div className="form-group">
                                            <label htmlFor="">Product Name</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="productname" value={this.state.productname} />
                                        </div>
                                    </div>

                                    <div className="mt-2">
                                        <label htmlFor="">Category</label>
                                        <select className="form-select" name="category" value={this.state.category} onChange={this.onChangeHandle}>
                                            <option value="">Select</option>
                                            {this.state.allCategoryArr.map((data, i) =>
                                                <option key={i} value={data.categoryname} >{data.categoryname}</option>
                                            )}
                                        </select>
                                    </div>

                                    <div className="mt-2">
                                        <div className="form-group">
                                            <label htmlFor="">Quantity</label>
                                            <input type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="qty" value={this.state.qty} />
                                        </div>
                                    </div>
                                    <div className="mt-2">
                                        <div className="form-group">
                                            <label htmlFor="">Product Details</label>
                                            <textarea type="text" onChange={this.onChangeHandle}
                                                className="form-control" name="details" value={this.state.details} ></textarea>
                                        </div>
                                    </div>

                                    <div className="mt-2">
                                        {this.state.editproduct ?
                                            <button className="btn btn-primary w-100" onClick={this.onClickUpdate}>Update</button> :
                                            <button className="btn btn-primary w-100" onClick={this.onClickSubmit}>Submit</button>}
                                    </div>


                                </div>

                            </div> : ''}
                    </div>
                </div>
            </div>
        );
    }
}

export default Products;