import axios from 'axios';
import moment from 'moment';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import Filters from '../../CommonComponents/Filters';
import '../../StyleSheets/Style.scss'

class ViewOrdersData extends Component {
    constructor(props) {
        super(props);
        this.state = {
            feildData:[{title:'Client ID', value:'clientid'}, {title:'Client Type', value:'clienttype'}, {title:'Order No',value:'idcode'}, {title:'Status', value:'status'}]
        }
        this.deleteOrders = this.deleteOrders.bind(this)
        this.editOrders = this.editOrders.bind(this)
        this.viewOrders = this.viewOrders.bind(this)
        this.searchBtn = this.searchBtn.bind(this)
    }


    deleteOrders(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode,
            status: 'Cancel'
        }
        axios.post(`${apiService.apiUrl()}/users/updateorders`, dataObject).then(() => {
            console.log(`orders ${e.idcode} canceled.`)
            this.props.getAllOrders()
        })
            .catch(error => console.error(`There was an error removing the ${e.idcode} Staff: ${error}`))
    }

    editOrders(e) {
        console.log(e);
        this.props.editOrders(e)
    }

    viewOrders(e, id) {
        var datas = {
            data: e.idcode,
            url: id.target.id
        }
        console.log(datas);
        this.props.viewOrders(datas)
        localStorage.setItem('singleOrderData', JSON.stringify(e))
    }

    searchBtn(data) {
        console.log(data)
        this.props.searchOrderData(data)
    }


    render() {
        return (
            <div>
                <div className="mt-3">
                    <div className="fieldset">
                        <span className="legend">All orders Data</span>

                        <Filters
                            data={{ tablename: 'orders', fieldname: 'orderdate', }}
                            fielddata={this.state.feildData}
                            searchBtn={this.searchBtn}
                        />

                        <div className="mt-2">
                            {this.props.data.length > 0 ?
                                <table className="table table-responsive table-hover">
                                    <thead>
                                        <tr>
                                            <th>Order No.</th>
                                            <th>Order Date</th>
                                            <th>Client ID</th>
                                            <th>Client Type</th>
                                            <th>Status</th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.props.data.map((data, i) =>
                                            <tr key={i} className={
                                                data.status === 'Cancel' ? "cancel" : '' ||
                                                    data.status === 'Completed' ? "completed" : '' ||
                                                        data.status === 'In Process' ? "inprocess" : '' ||
                                                            data.status === 'Pending' ? "" : ''
                                            }>
                                                <td>{data.idcode}</td>
                                                <td>{moment(data.orderdate).format('DD-MM-YYYY')}</td>
                                                <td>{data.clientid}</td>
                                                <td>{data.clienttype}</td>
                                                <td>{data.status}</td>
                                                <td>
                                                    <span className="text-info pointer" id={`${window.location.pathname}/vieworderdetails`} onClick={(id) => this.viewOrders(data, id)}>View</span>
                                                    {data.status === "Cancel" || data.status === "Completed" ? <span className="text-secondary mx-2">Edit</span> : <span className="text-primary mx-2 pointer" onClick={() => this.editOrders(data)}>Edit</span>}
                                                    {data.status === "Cancel" || data.status === "Completed" ? <span className="text-secondary">Cancel</span> : <span className="text-danger pointer" onClick={() => this.deleteOrders(data)}>Cancel</span>}
                                                </td>
                                            </tr>)}

                                    </tbody>
                                </table> : <div className="text-center fw-bold"> orders not found</div>}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewOrdersData;