import axios from 'axios';
import moment from 'moment';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewParcelData extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
        this.deleteParcels = this.deleteParcels.bind(this)
        this.editParcels = this.editParcels.bind(this)
        this.viewParcels = this.viewParcels.bind(this)
    }


    deleteParcels(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/parcelsdelete`, dataObject).then(() => {
            console.log(`parcels ${e.idcode} removed.`)
            this.props.getAllParcelsData()
        })
            .catch(error => console.error(`There was an error removing the ${e.idcode} Staff: ${error}`))
    }

    editParcels(e) {
        console.log(e);
        this.props.editParcels(e)
    }

    viewParcels(e, id) {
        var datas = {
            data: e,
            url: id.target.id
        }
        this.props.viewParcels(datas)
        localStorage.setItem('singleParcelData', JSON.stringify(e))
    }


    render() {
        return (
            <div>
                <div className="mt-3">
                <div className="fieldset">
                    <span className="legend">All Parcels Data</span>
                
                <div className="mt-2">
                    {this.props.data.length > 0 ?
                        <table className="table table-striped table-responsive table-hover">
                            <thead>
                                <tr>
                                    <th>Package id</th>
                                    <th>Ship. Code</th>
                                    <th>Pack Status</th>
                                    <th>Pack Date</th>
                                    <th>Ship. Name</th>
                                    <th>Ship. Status</th>
                                    <th>Last Update Date</th>
                                    <th>Comments</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.props.data.map((data, i) =>
                                    <tr key={i}>
                                        <td>{data.idcode}</td>
                                        <td>{data.shipmentcode}</td>
                                        <td>{data.packingstatus}</td>
                                        <td>{moment(data.packingdate).format('DD-MM-YYYY')}</td>
                                        <td>{data.shipmentname}</td>
                                        <td>{data.shipmentstatus}</td>
                                        <td>{moment(data.statusupdatedate).format('DD-MM-YYYY')}</td>
                                        <td>{data.comments}</td>
                                        <td>
                                            <span className="text-info me-1 pointer" id={`${window.location.pathname}/viewdetails`} onClick={(id) => this.viewParcels(data, id)}>View</span>
                                            <span className="text-primary me-1 pointer" onClick={() => this.editParcels(data)}>Edit</span>
                                            <span className="text-danger ms-1 pointer" onClick={() => this.deleteParcels(data)}>Delete</span>
                                        </td>
                                    </tr>)}

                            </tbody>
                        </table> : <div className="text-center fw-bold"> parcels not found</div>}
                </div>
            </div>
            </div>
            </div>
        );
    }
}

export default ViewParcelData;
