import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class PaymentReport extends Component {
    constructor(props) {
        super(props);
        this.state = {
            reposrdata: [],
            fromDate: '',
            toDate: ''
        }
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.onClickSearch = this.onClickSearch.bind(this)
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onClickSearch() {
        console.log(this.state.toDate, this.state.fromDate);
        var diffDate = Math.floor((Math.abs(new Date(this.state.toDate) - new Date(this.state.fromDate))) / (1000 * 60 * 60 * 24))
        console.log(diffDate);
        if (diffDate <= 91) {
            const dataObject = {
                fromdate: this.state.fromDate,
                todate: this.state.toDate
            }
            axios.post(`${apiService.apiUrl()}/users/paymentreportfilter`, dataObject).then(response => {
                console.log(response.data);
                this.setState({
                    reposrdata: response.data
                })
            }).catch(error => console.error(`Something went to wrong ${error}`))
        }else{
            alert("Please select TODATE under 90 days")
        }

    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="mt-5">
                        <div className="fieldset">
                            <span className="legend">Payment Report</span>

                            <div className="row">
                                <div className="col-3 form-group">
                                    <label htmlFor="">From</label>
                                    <input type="date" onChange={this.onChangeHandle}
                                        className="form-control" name="fromDate" value={this.state.fromDate} />
                                </div>
                                <div className="col-3 form-group">
                                    <label htmlFor="">To</label>
                                    <input type="date" onChange={this.onChangeHandle}
                                        className="form-control" name="toDate" value={this.state.toDate} />
                                </div>
                                <div className="col-3 mt-4">
                                    <button className="btn btn-primary" onClick={this.onClickSearch}>Search</button>
                                </div>
                            </div>

                            <div className="mt-3">
                                {this.state.reposrdata.length > 0 ?
                                    <div>
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <th>ID</th>
                                                    <th>Installment Amount</th>
                                                    <th>Date</th>
                                                    <th>Payment Type</th>
                                                    <th>Cheque No.</th>
                                                    <th>Bank Name</th>
                                                    <th>Status</th>
                                                    {/* <th>Action</th> */}
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.state.reposrdata.map((data, i) =>

                                                    <tr key={i} className={data.status === "Done"?"text-success":""}>
                                                        <td>{data.idcode}</td>
                                                        <td>{data.installmentamount}</td>
                                                        <td>{data.paymentdate}</td>
                                                        <td>{data.paymenttype === null ? <div>N/A</div> : <div>{data.paymenttype}</div>}</td>
                                                        <td>{data.chequeno === null ? <div>N/A</div> : <div>{data.chequeno}</div>}</td>
                                                        <td>{data.bankname === null ? <div>N/A</div> : <div>{data.bankname}</div>}</td>
                                                        <td>{data.status}</td>
                                                        {/* <td>
                                                            <button className="btn btn-primary btn-sm">Pay Now</button>
                                                        </td> */}
                                                    </tr>
                                                )}
                                            </tbody>
                                        </table>
                                    </div>
                                    :
                                    <div className="text-center my-5 fw-bold">Data Not Available</div>}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default PaymentReport;