import axios from 'axios';
import React, { Component } from 'react';
import { Modal, ModalBody, } from 'react-bootstrap';
import ModalHeader from 'react-bootstrap/esm/ModalHeader';
import apiService from '../../ApiService/ApiService';

class ViewPaymentDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            clientData: JSON.parse(localStorage.getItem('clientData')),
            billdetails: '',
            customerdetails: '',
            installmentdetails: [],
            paynow: false,
            singleemaidata:'',
            typeofTransaction:'',
            paystatus:'Done'

        }
        this.getSingleSaleBillDetails = this.getSingleSaleBillDetails.bind(this)
        this.getInstallmentDtails = this.getInstallmentDtails.bind(this)
        this.getCustomerDetails = this.getCustomerDetails.bind(this)
        this.onClickPrint = this.onClickPrint.bind(this)
        this.previewClose = this.previewClose.bind(this)
        this.payNowClick = this.payNowClick.bind(this)
        this.payNowClose = this.payNowClose.bind(this)
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.clickOnPay = this.clickOnPay.bind(this)
    }

    componentDidMount() {
        this.getSingleSaleBillDetails()
        this.setState({
            orderid: localStorage.getItem('clientData'),
        })
    }
    onChangeHandle(e){
        this.setState({
            [e.target.name]:e.target.value
        })
    }

    getSingleSaleBillDetails() {
        const dataObject = {
            orderid: this.state.clientData.orderid,
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/getsalebillsdatabyorderid`, dataObject).then(response => {
            console.log('selebillsdetails', response.data);
            this.setState({
                billdetails: response.data[0]
            })
            this.getInstallmentDtails()
        }).catch(error => console.error(`Something went to wrong ${error}
        `))
    }

    getInstallmentDtails() {
        const dataObject = {
            orderid: this.state.clientData.orderid
        }

        axios.post(`${apiService.apiUrl()}/users/getinstallmentbyorderid`, dataObject).then(response => {
            console.log('installment', response.data);
            this.setState({
                installmentdetails: response.data
            })
            this.getCustomerDetails()
        })
    }

    getCustomerDetails() {
        if (this.state.clientData.clienttype === 'Sale') {
            const dataObject = {
                idcode: this.state.billdetails.customerid
            }

            axios.post(`${apiService.apiUrl()}/users/getcustomerbyidcode`, dataObject).then(response => {
                console.log('customer', response.data);
                this.setState({
                    customerdetails: response.data[0]
                })
            })
        } else {
            const dataObject = {
                idcode: this.state.billdetails.customerid
            }

            axios.post(`${apiService.apiUrl()}/users/getvenderbyidcode`, dataObject).then(response => {
                console.log('customer', response.data);
                this.setState({
                    customerdetails: response.data[0]
                })
            })
        }

    }

    onClickPrint() {
        var win = window.open('', '', 'left=0,top=0,width=768,height=1020,toolbar=0,scrollbars=0,status =0');

        var content = "<html>";
        content += "<body onload=\"window.print(); window.close();\">";
        content += document.getElementById("emidetails").innerHTML;
        content += "</body>";
        content += "</html>";
        win.document.write(content);
        win.document.close();
    }

    previewClose() {
        this.props.history.push("/dashboard/orders")
    }

    payNowClick(e) {
        console.log(e);
        this.setState({
            paynow: true,
            singleemaidata:e
        })
    }
    payNowClose() {
        this.setState({
            paynow: false
        })
    }

    clickOnPay(){
        const dataObject = {
            'idcode':this.state.singleemaidata.idcode,
            'status':this.state.paystatus,
            'transactiontype':this.state.typeofTransaction,
        }
        axios.post(`${apiService.apiUrl()}/users/updateinstallments`, dataObject).then(response =>{
            console.log(response.data);
        }).catch(error => console.error(`something went to wrong ${error}`))
    }

    render() {
        return (
            <div>
                <div className="container" id="emidetails">
                    <div className="p-3">
                        <div className="row">
                            <div className="col-6 p-3 border">
                                <div className="row">
                                    <div className="col">
                                        <small className="text-secondary">Customer ID:</small>
                                        <div>{this.state.customerdetails.idcode}</div>

                                        <small className="text-secondary">Name:</small>
                                        <div>{this.state.customerdetails.name}</div>

                                        <small className="text-secondary">Contact No.:</small>
                                        <div>{this.state.customerdetails.contactno}</div>
                                    </div>
                                    <div className="col">
                                        <small className="text-secondary">Address:</small>
                                        <div>{this.state.customerdetails.address}</div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    <hr />

                    <div className="col border p-3">
                        <div className="row">
                            <div className="col-3">
                                <small className="text-secondary">Order No.</small>
                                <div>{this.state.billdetails.orderid}</div>
                            </div>
                            <div className="col-3">
                                <small className="text-secondary">Bill Date.</small>
                                <div>{this.state.billdetails.billdate}</div>
                            </div>
                            <div className="col-3">
                                <small className="text-secondary">Total Amount.</small>
                                <div>{this.state.billdetails.totalamount}</div>
                            </div>
                            <div className="col-3">
                                <small className="text-secondary">Downpayment.</small>
                                <div>{this.state.billdetails.downpayment}</div>
                            </div>
                            <div className="col-3 mt-3">
                                <small className="text-secondary">Warranty.</small>
                                <div>{this.state.billdetails.warranty} year</div>
                            </div>
                            <div className="col-3 mt-3">
                                <small className="text-secondary">Installment Start On.</small>
                                <div>{this.state.billdetails.installmentdate}</div>
                            </div>
                            <div className="col-12 mt-3" style={{ fontSize: '12px' }}>
                                <span className="fw-bold">Note:</span><small className="text-secondary"> If cheque will bounce of any reason then we will apply penalty </small><span>₹ {this.state.billdetails.penalty} /-</span>
                            </div>
                        </div>
                    </div>
                    <hr />
                    <div className="col border p-3 my-3">
                        <table class="table">
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th>Installment Amount</th>
                                    <th>Date</th>
                                    <th>Trans. Type</th>
                                    <th>Payment Type</th>
                                    <th>Cheque No.</th>
                                    <th>Bank Name</th>
                                    <th>Status</th>
                                    <th>Action</th>

                                </tr>
                            </thead>
                            <tbody>
                                {console.log(this.state.installmentdetails)}
                                {this.state.installmentdetails.map((data, i) =>

                                    <tr>
                                        <td>{data.idcode}</td>
                                        <td>{parseFloat(data.installmentamount).toFixed(2)}</td>
                                        <td>{data.paymentdate}</td>
                                        <td>{data.transactiontype}</td>
                                        <td>{data.paymenttype === null ? <div>N/A</div> : <div>{data.paymenttype}</div>}</td>
                                        <td>{data.chequeno === null ? <div>N/A</div> : <div>{data.chequeno}</div>}</td>
                                        <td>{data.bankname === null ? <div>N/A</div> : <div>{data.bankname}</div>}</td>
                                        <td>{data.status}</td>
                                        <td>
                                            {data.status === "Pending" ?
                                                <button className="btn btn-sm btn-primary" onClick={() => this.payNowClick(data)}>Pay Now</button> :
                                                <button className="btn btn-sm btn-success">Paid</button>}
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                    </div>


                </div>

                <Modal show={this.state.paynow} onHide={this.payNowClose}>
                    <ModalHeader>
                        Pay Now
                    </ModalHeader>
                    <ModalBody>
                            <div>
                                <div>Installment ID</div>
                                <p>{this.state.singleemaidata.idcode}</p>
                                <div>Installment Amount</div>
                                <p>₹ {parseFloat(this.state.singleemaidata.installmentamount).toFixed(2)}</p>
                                 <div className="form-group">
                                   <label htmlFor="">Transaction Type</label>
                                   <input type="text" onChange={this.onChangeHandle}
                                     className="form-control" name="typeofTransaction" value={this.state.typeofTransaction} />
                                 </div>
                                 <div className="form-group">
                                   <label htmlFor="">Transaction Type</label>
                                   <select type="text" onChange={this.onChangeHandle}
                                     className="form-control" name="paystatus" value={this.state.paystatus} >
                                         <option value="">Select</option>
                                         <option value="Pending">Pending</option>
                                         <option value="Done">Done</option>
                                     </select>
                                 </div>
                                 <div className="mt-3">
                                    <button className="btn btn-primary" onClick={this.clickOnPay}>Pay</button>
                                 </div>
                            </div>
                    </ModalBody>
                </Modal>
                <div className="my-3 text-center">
                    <button className="btn btn-primary me-1" onClick={this.onClickPrint}>Print</button>
                    <button className="btn btn-danger ms-1" onClick={this.previewClose}>Close</button>
                </div>
            </div>
        );
    }
}

export default ViewPaymentDetails;