import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewVendorsData extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }

        this.updateVendors = this.updateVendors.bind(this)
        this.deleteVendors = this.deleteVendors.bind(this)
    }


    updateVendors(e){
        this.props.editVendors(e)
    }

    deleteVendors(e){
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/vendorsdelete`, dataObject).then(() => {
            console.log(`vendors ${e.name} removed.`)
            this.props.getAllvendors()
        })
            .catch(error => console.error(`There was an error removing the ${e.name} Product: ${error}`))
    }
    
    render() {
        return (
            <div>
                <div className="mt-3">
                <div className='fieldset'>
                    <span className="legend">All Vendors</span>
               
                {this.props.data.length > 0 ?
                    <table className="table table-striped table-hover">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>Vender Name</th>
                                <th>Email id</th>
                                <th>Contact No.</th>
                                <th>Address</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>

                            {this.props.data.map((data, i) =>
                                <tr key={i}>
                                    <td>{data.id}</td>
                                    <td>{data.name}</td>
                                    <td>{data.email}</td>
                                    <td>{data.contactno}</td>
                                    <td>{data.address}</td>
                                    <td>
                                        <span className="text-primary me-1 pointer" onClick={() => this.updateVendors(data)}>Edit</span>
                                        <span className="text-danger ms-1 pointer" onClick={() => this.deleteVendors(data)}>Delete</span>
                                    </td>
                                </tr>
                            )}


                        </tbody>
                    </table> : <div className="text-center fw-bold"> vendors not found</div>}
            </div>
            </div>
            </div>
        );
    }
}

export default ViewVendorsData;