import axios from 'axios';
import moment from 'moment';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class Salebills extends Component {
    constructor(props) {
        super(props);
        this.state = {
            paymentData: JSON.parse(localStorage.getItem('paymentdata')),
            orderid: '',
            customerid: '',
            billdate: '',
            category: '',
            product: '',
            totalamount: '',
            downpayment: '',
            warranty: '',
            penalty: '',
            installmentdate: '',
            installment: '',
            salesbillsArr: [],
            installmentArr: [],
            singleInsallmentData: '',
            editemi: '',
            emiamount: '',
            emidate: '',
            type: '',
            chqno: '',
            bank: '',
            status: 'Pending',
            addnewinstallment: false

        }
        this.getAllSalesBill = this.getAllSalesBill.bind(this)
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.getInstallmentData = this.getInstallmentData.bind(this)
        this.editInstallment = this.editInstallment.bind(this)
        this.saveEditEmi = this.saveEditEmi.bind(this)
        this.addNewEmi = this.addNewEmi.bind(this)
        this.addotheremi = this.addotheremi.bind(this)
        this.addotheremi = this.addotheremi.bind(this)
        this.onClickNextBtn = this.onClickNextBtn.bind(this)
    }


    componentDidMount() {
        this.getAllSalesBill()
        this.getInstallmentData()
        var paymetdata = JSON.parse(localStorage.getItem('paymentdata'))
        this.setState({
            orderid: paymetdata.orderid,
            customerid: paymetdata.customerid,
            totalamount: paymetdata.totalamount,
        })
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        }, () => {
            if (e.target.name === 'billdate') {
                var billdate = new Date(this.state.billdate)
                billdate.setMonth(billdate.getMonth() + 1)
                console.log(billdate);
                this.setState({
                    installmentdate: moment(billdate).format('YYYY-MM-DD')
                })
            }
        })
    }

    getAllSalesBill() {
        axios.get(`${apiService.apiUrl()}/users/salebillsall`).then(response => {
            console.log(response.data)
            this.setState({
                salesbillsArr: response.data
            })
        })
    }

    getInstallmentData() {
        const dataObject = {
            orderid: this.state.paymentData.orderid
        }
        axios.post(`${apiService.apiUrl()}/users/getinstallmentbyorderid`, dataObject).then(response => {
            console.log(response.data)
            this.setState({
                installmentArr: response.data
            })
        })
    }


    onSubmit() {
        const remainAmount = this.state.totalamount - this.state.downpayment
        const installmentAmount = remainAmount / this.state.installment
        const emiArr = []
        const dataObject = {
            orderid: this.state.orderid,
            installmentamount: this.state.downpayment === 0 ? remainAmount : installmentAmount,
            paymentdate: moment(this.state.installmentdate).format('YYYY-MM-DD'),
            status: 'Pending'
        }
        emiArr.push(dataObject)
        var current = new Date(this.state.installmentdate);
        for (let i = 1; i < this.state.installment; i++) {
            console.log(installmentAmount);
            console.log(current);
            var emiDate = current.setMonth(current.getMonth() + 1);

            const dataObject = {
                orderid: this.state.orderid,
                installmentamount: installmentAmount,
                paymentdate: moment(emiDate).format('YYYY-MM-DD'),
                status: 'Pending'
            }
            emiArr.push(dataObject)
        }


        const reqData = {
            orderid: this.state.orderid,
            customerid: this.state.customerid,
            billdate: this.state.billdate,
            totalamount: this.state.totalamount,
            downpayment: this.state.downpayment,
            warranty: this.state.warranty,
            penalty: this.state.penalty,
            installmentdate: this.state.installmentdate,
            installment: this.state.installment
        }
        console.log(reqData);
        console.log(emiArr);

        axios.post(`${apiService.apiUrl()}/users/createsalebills`, reqData).then(response => {
            console.log(response.data);

            if (response.data) {
                for (let i = 0; i < emiArr.length; i++) {
                    var data = emiArr[i]
                    axios.post(`${apiService.apiUrl()}/users/createinstallments`, data).then(response => {
                        console.log(response.data);
                    }).catch(error => console.error(`something went to wrong ${error}`))
                }

                this.getInstallmentData()
            }

        }).catch(error => console.error(`something went to wrong ${error}`))
    }

    editInstallment(e) {
        console.log(e);

        if (e.idcode) {
            this.setState({
                singleInsallmentData: e,
                editemi: e.idcode,
                emiamount: e.installmentamount,
                emidate: e.paymentdate,
                type: e.paymenttype,
                chqno: e.chequeno,
                bank: e.bankname,
                status: e.status,
            })
        }

    }

    saveEditEmi() {
        const dataObject = {
            idcode: this.state.singleInsallmentData.idcode,
            orderid: this.state.singleInsallmentData.orderid,
            installmentamount: this.state.emiamount,
            paymentdate: this.state.emidate,
            paymenttype: this.state.type,
            chequeno: this.state.chqno,
            bankname: this.state.bank,
            status: this.state.status,
        }

        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/updateinstallments`, dataObject).then(response => {
            console.log(response.data);
            this.getInstallmentData()
            this.setState({
                editemi: ''
            })
        }).catch(error => console.error(`something went to wrong ${error}`))
    }

    addotheremi() {
        this.setState({
            addnewinstallment: !this.state.addnewinstallment
        })
    }

    addNewEmi() {
        const dataObject = {
            orderid: this.state.orderid,
            installmentamount: this.state.emiamount,
            paymentdate: this.state.emidate,
            paymenttype: this.state.type,
            chequeno: this.state.chqno,
            bankname: this.state.bank,
            status: this.state.status,
        }

        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/createinstallments`, dataObject).then(response => {
            console.log(response.data);
            this.addotheremi()
            this.getInstallmentData()
        }).catch(error => console.error(`something went to wrong ${error}`))
    }

    onClickNextBtn() {
        var clientData = {
            orderid: this.state.paymentData.orderid
        }
        localStorage.setItem('clientData', JSON.stringify(clientData))
        this.props.history.push('/dashboard/viewpaymentdetails')
    }


    render() {
        return (
            <div>
                <div className="container">
                    <div className="mt-5">
                        <div className="fieldset">
                            <span className="legend">Add New</span>

                            <div className="row">
                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Customer Name</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="customerid" value={this.state.customerid} />
                                    </div>
                                </div>

                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Bill Date</label>
                                        <input type="date" onChange={this.onChangeHandle}
                                            className="form-control" name="billdate" value={this.state.billdate} />
                                    </div>
                                </div>


                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Total Amount</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="totalamount" value={this.state.totalamount} readOnly />
                                    </div>
                                </div>

                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Down Payment</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="downpayment" value={this.state.downpayment} />
                                    </div>
                                </div>

                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Warranty</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="warranty" value={this.state.warranty} />
                                    </div>
                                </div>

                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Penalty</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="penalty" value={this.state.penalty} />
                                    </div>
                                </div>

                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Installment Date</label>
                                        <input type="date" onChange={this.onChangeHandle}
                                            className="form-control" name="installmentdate" value={this.state.installmentdate} />
                                    </div>
                                </div>

                                <div className="col-3">
                                    <div className="form-group">
                                        <label htmlFor="">Installment</label>
                                        <input type="text" onChange={this.onChangeHandle}
                                            className="form-control" name="installment" value={this.state.installment} />
                                    </div>
                                </div>

                                <div className="my-3">
                                    {this.state.paymentdate === '' || this.state.downpayment === '' || this.state.warranty === '' || this.state.penalty === '' || this.state.installmentdate === '' || this.state.installment === '' ?
                                        <button className="btn btn-primary" disabled>Submit</button> :
                                        <button className="btn btn-primary" onClick={this.onSubmit}>Submit</button>}
                                </div>
                                {this.state.installmentArr.length > 0 ?
                                    <div className="mt-3">
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <th>ID</th>
                                                    <th>Installment Amount</th>
                                                    <th>Date</th>
                                                    <th>Payment Type</th>
                                                    <th>Cheque No.</th>
                                                    <th>Bank Name</th>
                                                    <th>Status</th>
                                                    <th>Action</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.state.installmentArr.map((data, i) =>
                                                    <tr key={i}>
                                                        <td>{data.idcode}</td>

                                                        <td>
                                                            {this.state.editemi === data.idcode ?
                                                                <div>
                                                                    <div className="form-group">
                                                                        <input type="text"
                                                                            className="form-control" name="emiamount" value={this.state.emiamount} onChange={this.onChangeHandle} />
                                                                    </div>
                                                                </div> : <div>{parseFloat(data.installmentamount).toFixed(2)}</div>}

                                                        </td>

                                                        <td>
                                                            {this.state.editemi === data.idcode ?
                                                                <div>
                                                                    <div className="form-group">
                                                                        <input type="date"
                                                                            className="form-control" name="emidate" value={this.state.emidate} onChange={this.onChangeHandle} />
                                                                    </div>
                                                                </div> : <div>{data.paymentdate}</div>}

                                                        </td>


                                                        <td>
                                                            {this.state.editemi === data.idcode ?
                                                                <div>
                                                                    <div className="form-group">
                                                                        <select type="text"
                                                                            className="form-select" name="type" value={this.state.type} onChange={this.onChangeHandle} >
                                                                            <option value="">Select</option>
                                                                            <option value="cash">Cash</option>
                                                                            <option value="cheque">Cheque</option>
                                                                        </select>
                                                                    </div>
                                                                </div> : <div>{data.paymenttype === null ? <div>-</div> : <div>{data.paymenttype}</div>}</div>}

                                                        </td>

                                                        <td>
                                                            {this.state.editemi === data.idcode ?
                                                                <div>
                                                                    <div className="form-group">
                                                                        <input type="text"
                                                                            className="form-control" name="chqno" value={this.state.chqno} onChange={this.onChangeHandle} />
                                                                    </div>
                                                                </div> : <div>{data.chequeno === null ? <div>-</div> : <div>{data.chequeno}</div>}</div>}

                                                        </td>
                                                        <td>
                                                            {this.state.editemi === data.idcode ?
                                                                <div>
                                                                    <div className="form-group">
                                                                        <input type="text"
                                                                            className="form-control" name="bank" value={this.state.bank} onChange={this.onChangeHandle} />
                                                                    </div>
                                                                </div> : <div>{data.bankname === null ? <div>-</div> : <div>{data.bankname}</div>}</div>}

                                                        </td>

                                                        <td>
                                                            {this.state.editemi === data.idcode ?
                                                                <div>
                                                                    <div className="form-group">
                                                                        <select type="text"
                                                                            className="form-select" name="status" value={this.state.status} onChange={this.onChangeHandle} >
                                                                            <option value="">Select</option>
                                                                            <option value="Pending">Pending</option>
                                                                            <option value="Done">Done</option>
                                                                        </select>
                                                                    </div>
                                                                </div> : <div>{data.status === null ? <div>-</div> : <div>{data.status}</div>}</div>}

                                                        </td>

                                                        <td>
                                                            <div>
                                                                {this.state.editemi === data.idcode ?
                                                                    <span className="me-2 text-primary" onClick={() => this.saveEditEmi()}>Save</span> :
                                                                    <span className="me-2 text-primary" onClick={(e) => this.editInstallment(data)}>Edit</span>}
                                                                <span className="text-danger">Delete</span>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                )}

                                            </tbody>
                                        </table>

                                        <div className="mt-3 py-3">
                                            <div className="text-primary" onClick={this.addotheremi}>+ Add new</div>
                                        </div>

                                        {this.state.addnewinstallment ?

                                            <div className="py-3">
                                                <div className="row">
                                                    <div className="col">
                                                        <div className="form-group">
                                                            <label htmlFor="">Installment Amount</label>
                                                            <input type="text" onChange={this.onChangeHandle}
                                                                className="form-control" name="emiamount" value={this.state.emiamount} />
                                                        </div>
                                                    </div>
                                                    <div className="col">
                                                        <div className="form-group">
                                                            <label htmlFor="">Date</label>
                                                            <input type="date" onChange={this.onChangeHandle}
                                                                className="form-control" name="emidate" value={this.state.emidate} />
                                                        </div>
                                                    </div>
                                                    <div className="col">
                                                        <div className="form-group">
                                                            <label htmlFor="">Payment Type</label>
                                                            <select type="text"
                                                                className="form-select" name="type" value={this.state.type} onChange={this.onChangeHandle} >
                                                                <option value="">Select</option>
                                                                <option value="cash">Cash</option>
                                                                <option value="cheque">Cheque</option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                    <div className="col">
                                                        <div className="form-group">
                                                            <label htmlFor="">Cheque No.</label>
                                                            <input type="text" onChange={this.onChangeHandle}
                                                                className="form-control" name="chqno" value={this.state.chqno} />
                                                        </div>
                                                    </div>
                                                    <div className="col">
                                                        <div className="form-group">
                                                            <label htmlFor="">Bank Name</label>
                                                            <input type="text" onChange={this.onChangeHandle}
                                                                className="form-control" name="bank" value={this.state.bank} />
                                                        </div>
                                                    </div>
                                                    <div className="col">
                                                        <div className="form-group">
                                                            <label htmlFor="">Status</label>
                                                            <select type="text"
                                                                className="form-select" name="status" value={this.state.status} onChange={this.onChangeHandle} >
                                                                <option value="">Select</option>
                                                                <option value="Pending">Pending</option>
                                                                <option value="Done">Done</option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="mt-3 text-end">
                                                    <button className="btn btn-primary px-5" onClick={this.addNewEmi}>Add</button>
                                                </div>
                                            </div> : ''}
                                        <div className="my-3">
                                            {this.state.addnewinstallment === true && (this.state.emiamount === '' || this.state.emidate === '' || this.state.type === '') ?
                                                <button className="btn btn-primary" disabled >Next</button> :
                                                <button className="btn btn-primary" onClick={this.onClickNextBtn}>Next</button>}
                                        </div>

                                    </div> : ''}


                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Salebills;