import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';

class ViewShipmentData extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }

        this.updateShipmentservice = this.updateShipmentservice.bind(this)
        this.deleteShipmentservice = this.deleteShipmentservice.bind(this)
    }


    updateShipmentservice(e){
        this.props.editShipmentservice(e)
    }

    deleteShipmentservice(e){
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/shipmentservicedelete`, dataObject).then(() => {
            console.log(`shipmentservice ${e.name} removed.`)
            this.props.getAllshipmentservice()
        })
            .catch(error => console.error(`There was an error removing the ${e.name} Product: ${error}`))
    }
    
    render() {
        return (
            <div>
                <div className="mt-3">
                <div className="fieldset">
                    <span className="legend">All Shipment Service</span>
                
                {this.props.data.length > 0 ?
                    <table className="table table-striped table-hover">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>Service Name</th>
                                <th>Email id</th>
                                <th>Contact No.</th>
                                <th>Address</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>

                            {this.props.data.map((data, i) =>
                                <tr key={i}>
                                    <td>{data.id}</td>
                                    <td>{data.name}</td>
                                    <td>{data.email}</td>
                                    <td>{data.contactno}</td>
                                    <td>{data.address}</td>
                                    <td>
                                        <span className="text-primary me-1 pointer" onClick={() => this.updateShipmentservice(data)}>Edit</span>
                                        <span className="text-danger ms-1 pointer" onClick={() => this.deleteShipmentservice(data)}>Delete</span>
                                    </td>
                                </tr>
                            )}


                        </tbody>
                    </table> : <div className="text-center fw-bold"> shipmentservice not found</div>}
            </div>
            </div>
            </div>
        );
    }
}

export default ViewShipmentData;