import axios from 'axios';
import React, { Component } from 'react';
import apiService from '../../ApiService/ApiService';
import ViewDetails from '../../CommonComponents/ViewDetails';

class ViewProductsData extends Component {
    imageArr = []
    constructor(props) {
        super(props);
        this.state = {
            addDetailsForm: false,
            imageUploadBtn: false,
            imagesArr: [],
            existImagesArr: [],
            singleProductData: '',
            productid: '',
            size: '',
            width: '',
            height: '',
            weight: '',
            colour: '',
            material: '',
            mrpprice:'',
            saleprice:'',
            group: '',
            images: '',
            detailsOfProduct: '',
            viweMoreDeatials: false

        }

        this.updateProducts = this.updateProducts.bind(this)
        this.deleteProducts = this.deleteProducts.bind(this)
        this.addDetails = this.addDetails.bind(this)
        this.submitAddDetails = this.submitAddDetails.bind(this)
        this.viewDetails = this.viewDetails.bind(this)
        this.onSelectFiles = this.onSelectFiles.bind(this)
        this.removeImage = this.removeImage.bind(this)
        this.onChangeHandle = this.onChangeHandle.bind(this)
        this.addImages = this.addImages.bind(this)
        this.uplodaImages = this.uplodaImages.bind(this)
        this.closeView = this.closeView.bind(this)
        this.removefile = this.removefile.bind(this)
    }

    onChangeHandle(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }


    updateProducts(e) {
        this.props.editProducts(e)
    }

    deleteProducts(e) {
        console.log(e);
        const dataObject = {
            idcode: e.idcode
        }
        axios.put(`${apiService.apiUrl()}/users/productsdelete`, dataObject).then(() => {
            console.log(`Products ${e.name} removed.`)
            this.props.getAllProducts()
        })
            .catch(error => console.error(`There was an error removing the ${e.name} Product: ${error}`))
    }

    addDetails(e) {
        console.log(e);
        this.setState({
            addDetailsForm: true,
            productid: e.idcode,
        }, () => {
            const dataObject = {
                productid: e.idcode
            }
            axios.post(`${apiService.apiUrl()}/users/getproductsbyproductid`, dataObject).then(response => {
                console.log(response.data);
                var datas = response.data[0]

                if (response.data) {
                    this.setState({
                        size: datas.size === null?'':datas.size,
                        width: datas.width === null?'':datas.width,
                        height: datas.height === null?'':datas.height,
                        weight: datas.weight === null?'':datas.weight,
                        colour: datas.colour === null?'':datas.colour,
                        material: datas.material === null? '':datas.material,
                        mrpprice: datas.mrpprice === null? '':datas.mrpprice,
                        saleprice: datas.saleprice === null? '':datas.saleprice,
                        group: datas.group === null?'':datas.group ,
                        images: datas.images === null?'':datas.images,
                        existImagesArr:datas.images ===null?null: JSON.parse(datas.images),
                    })
                }
            }).catch(error => console.error(`Something went to wrong! ${error}`))

        })


    }

    submitAddDetails() {
        const dataObject = {
            productid: this.state.productid,
            size: this.state.size,
            width: this.state.width,
            height: this.state.height,
            weight: this.state.weight,
            colour: this.state.colour,
            material: this.state.material,
            mrpprice: this.state.mrpprice,
            saleprice: this.state.saleprice,
            group: this.state.group,
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/updateproductsdetails`, dataObject).then(response => {
            console.log(response.data);
            if (response.data) {
                console.log('successfully added');
            } else {
                alert('something went to wrong!')
            }
        }).catch(error => console.error(`Something went wrong ${error}`))
    }

    viewDetails(e) {
        console.log(e)
        const dataObject = {
            productid: e.idcode
        }
        axios.post(`${apiService.apiUrl()}/users/getproductsbyproductid`, dataObject).then(response => {
            console.log(response.data);
            if (response.data) {
                this.setState({
                    detailsOfProduct: response.data,
                    viweMoreDeatials: true
                })
            }
        }).catch(error => console.error(`Something went to wrong! ${error}`))
    }

    onSelectFiles(e) {
        console.log(e.target.files.length);

        var filesArr = e.target.files


        if (filesArr.length > 10) {
            alert('Allow only 10 Images')
        } else {
            for (let i = 0; i < filesArr.length; i++) {
                var data = filesArr[i]
                console.log(data);
                this.imageArr.push(data)
            }
        }
        console.log(this.imageArr);
        this.setState({
            imagesArr: this.imageArr
        })
    }

    removeImage(e) {
        console.log(e);
        var newlist = this.imageArr.splice(e, 1)
        this.setState({
            imageArr: newlist
        })
    }

    addImages() {
        this.setState({
            imageUploadBtn: !this.state.imageUploadBtn,
            imagesArr: []
        })
    }

    uplodaImages() {

        var formData = new FormData()
        for (var i = 0; i < this.state.imagesArr.length; i++) {
            var data = this.state.imagesArr[i]
            console.log(data);
            formData.append('file', data)
        }
        formData.append('productid', this.state.productid)
        axios.post(`${apiService.apiUrl()}/users/multipleimageUpload`, formData).then(res => {
            console.log(res);
            this.setState({
                imageUploadBtn: !this.state.imageUploadBtn,
                imagesArr: []
            })
        })
    }

    closeView(data) {
        console.log(data);
        this.setState({
            viweMoreDeatials: false
        })
    }
        
    removefile(e){
        console.log(e);
        const dataObject = {
            filename:e,
            productid:this.state.productid
        }
        console.log(dataObject);
        axios.post(`${apiService.apiUrl()}/users/deletefile`, dataObject).then(respose => {
            console.log(respose.data);
        })
    }

    render() {
        return (
            <div>
                {this.state.addDetailsForm ?
                    <div>
                        <div>
                            <div className="row">
                                <div className="col"><div className="fs-5 badge bg-secondary">Add Details</div></div>
                                <div className="col text-end"><div className="fs-5 badge bg-danger pointer" onClick={() => { this.setState({ addDetailsForm: false }) }}>X</div></div>
                            </div>
                        </div>
                        <div className="fw-bold">
                            <p>ID: {this.state.productid}</p>
                        </div>
                        <div className="row">
                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Size</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="size" value={this.state.size} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Width</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="width" value={this.state.width} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Height</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="height" value={this.state.height} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Weight</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="weight" value={this.state.weight} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Colour</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="colour" value={this.state.colour} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Materials</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="material" value={this.state.material} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">MRP Price</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="mrpprice" value={this.state.mrpprice} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Sale Price</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="saleprice" value={this.state.saleprice} />
                                </div>
                            </div>

                            <div className="col-3">
                                <div className="form-group">
                                    <label htmlFor="">Group</label>
                                    <input type="text" onChange={this.onChangeHandle}
                                        className="form-control" name="group" value={this.state.group} />
                                </div>
                            </div>
                            <div className="col-3">
                                <button className="btn btn-primary mt-4 w-100" onClick={this.submitAddDetails}>Submit</button>
                            </div>
                            <div className="col-12 mt-2">
                                <div>
                                    <div>
                                        {this.state.imageUploadBtn ?
                                            <span className="text-danger pointer" onClick={this.addImages}>x Close</span> :
                                            <span className="text-primary pointer" onClick={this.addImages}>+ Add Image</span>}
                                    </div>
                                    {this.state.imageUploadBtn ?
                                        <div className="mt-3">
                                            <input name="file" type="file" ref={fileInput => this.fileInput = fileInput} onChange={this.onSelectFiles}
                                                className="form-control d-none" multiple />
                                            <button className="btn btn-primary " onClick={() => this.fileInput.click()}>Choose Files</button>
                                        </div>
                                        : ''}
                                    {this.state.imagesArr.length > 0 ?

                                        <div className="row">

                                            {this.state.imagesArr.map((data, i) =>
                                                <div key={i} className="col-2 p-2">
                                                    <img src={URL.createObjectURL(data)} className="w-100" height="100" alt="" />
                                                    {/* <img src={`${apiService.apiUrl()}/users/images/${this.state.productid}/${data}`} className="w-100" height="100" alt="" /> */}
                                                    <span className="text-danger pointer" onClick={() => this.removeImage(i)}>Remove</span>
                                                </div>
                                            )}
                                        </div>
                                        :
                                        <div >
                                            {this.state.existImagesArr === null?<div>Image not available</div>:
                                            <div className="row">
                                                {this.state.existImagesArr.map((data, i) =>
                                                <div key={i} className="col-2 p-2">
                                                    {data}
                                                    <img src={`${apiService.apiUrl()}/users/images/${this.state.productid}/${data}`} className="w-100" height="100" alt="" />
                                                    <span className="text-danger pointer" onClick={() => this.removefile(data)}>Remove</span>
                                                </div>
                                            )}
                                            </div>}
                                            
                                        </div>
                                    }
                                </div>
                            </div>

                            {this.state.imagesArr.length > 0 ?
                                <div className="py-3"><button className="btn btn-primary" onClick={this.uplodaImages}>Upload</button></div> : ''
                            }
                        </div>
                    </div> : this.state.viweMoreDeatials ?
                        <div>

                            <ViewDetails data={this.state.detailsOfProduct} productid={this.state.detailsOfProduct[0].productid} closeView={this.closeView} />
                        </div>
                        :
                        <div className="mt-3">
                            <div className="fieldset">
                                <span className="legend">All Products</span>
                           
                            {this.props.data.length > 0 ?
                                <table className="table table-striped table-hover">
                                    <thead>
                                        <tr>
                                            <th>ID</th>
                                            <th>Product Name</th>
                                            <th>Category Name</th>
                                            <th>Product Qty</th>
                                            <th>Product Details</th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>

                                        {this.props.data.map((data, i) =>
                                            <tr key={i}>
                                                <td>{data.id}</td>
                                                <td>{data.productname}</td>
                                                <td>{data.category}</td>
                                                <td>{data.qty}</td>
                                                <td>{data.details}</td>
                                                <td>
                                                    <span className="text-primary me-1 pointer" onClick={() => this.addDetails(data)}>Add Details</span>
                                                    <span className="text-primary mx-1 pointer" onClick={() => this.viewDetails(data)}>View</span>
                                                    <span className="text-primary mx-1 pointer" onClick={() => this.updateProducts(data)}>Edit</span>
                                                    <span className="text-danger ms-1 pointer" onClick={() => this.deleteProducts(data)}>Delete</span>
                                                </td>
                                            </tr>
                                        )}


                                    </tbody>
                                </table> : <div className="text-center fw-bold"> Products not found</div>}
                                </div>
                        </div>}
            </div>
        );
    }
}

export default ViewProductsData;

